<template>
  <Card style="width: 340px" class="cc">
    <div class="personIntro">
      <!--跳转到作者页面-->
      <div class="left">
        <img :src="author.avatar">
      </div>
      <div class="right">
        <div class="name">
          <!--跳转到作者页面-->
          <a>{{author.name}}</a>
          <i v-if="author.vip==1">V认证</i>
        </div>
        <!--关注-->
        <div class="relation">
          <Button v-if="author.attention==0" size="small" icon="plus-round" type="primary"
                  @click="attentionAuthorYES">关注</Button>
          <Button v-else size="small" type="primary" disabled>已关注</Button>
        </div>
      </div>
    </div>
    <div class="article">
      <h4 class="hi">推荐文章</h4>
      <ul>
        <li v-for="item in author.production">
          <!--跳转到文章页面-->
          <router-link target="_blank" :to="{path:'/otherImgText',query:{articleId:item.id}}">
            <a style="font-weight: 400" class="dd">{{item.title}}</a>
          </router-link>
        </li>
      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      author: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    methods: {
      attentionAuthorYES() {
        this.$emit("attentionAuthorYES")
      }
    }
  }

</script>

<style lang="less" scoped>
  .personIntro {
    flex: 1;
    top: 0px;
    left: 25px;
    letter-spacing: 0;
  }

  .hi{
    font-weight: 700;
    color: #000000;
  }

  .left {
    display: inline-block;
  }

  .left img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
  }

  .left img:hover {
    cursor: pointer;
  }

  .right {
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
  }

  .name {
    display: block;
    font-size: 18px;
  }

  .name a {
    color: #222222;
  }

  .name i {
    font-size: 10px;
    color: #ed4040;
    border: 1px solid #ed4040;
    padding: 0px 6px;
    border-radius: 5px;
  }

  .name a:hover {
    color: #406599;
  }

  .relation {
    display: inline-block;
    margin-top: 5px;
    margin-right: 5px;
  }

  .relation a {
    margin-top: 10px;
    font-size: 14px;
    letter-spacing: 1px;
    background-color: #2d8cf0;
    padding: 2px;
    border-radius: 3px;
    color: #ffffff;
  }

  .article {
    margin-top: 10px;
  }

  .article h4 {
    margin-bottom: 8px;
  }

  .article li {
    border-bottom: 1px solid #ffffff;
    padding-bottom: 8px;
    padding-top: 8px;
  }

  .dd{
    font-weight: 700;
    line-height: 24px;
  }

  .dd:hover{
    color: #406599;
  }

</style>



