<template>
  <Card class="share cc">
    <!--分享-->
    <div>
      <h4>分享至:</h4>
      <span class="share-conCss">
        <img class="img" src="../../../../../static/img/新浪.svg">
        <img class="img" src="../../../../../static/img/weixin.svg">
        <img class="img" src="../../../../../static/img/QQ空间.svg">
      </span>
    </div>
    <!--收藏-->
    <div>
      <div class="share-description">
      <span>
        <span v-if="collection==0" class="share-des" @click="collectArticleYES">
          <Icon class="fontCss" type="ios-star-outline"></Icon>收藏
        </span>
        <span v-else class="share-des yes" @click="collectArticleNO">
          <Icon class="fontCss" type="ios-star"></Icon>已收藏
        </span>
        <span class="share-des" @click="modal1 = true">
          <Icon class="fontCss" type="ios-information-outline"></Icon>举报
        </span>
      </span>
        <span></span>
      </div>
    </div>

    <!--举报-->
    <Modal class="report" v-model="modal1" @on-ok="ok" @on-cancel="cancel">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>举报文章</span>
      </p>
      <RadioGroup v-model="type" vertical>
        <Radio v-for="(item,index) in report" :label="item.reportId">
          <span>{{item.reportType}}</span>
        </Radio>
        <Input v-model="content" style="width: 488px;margin-top: 10px;" type="textarea" :rows="4" placeholder="我有话说..."></Input>
      </RadioGroup>
    </Modal>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      collection: Number,
      PRIFIX: String
    },
    data() {
      return {
        modal1: false,
        type: -1,
        content: "",
        report: Array
      }
    },
    methods: {
      collectArticleYES() {
        this.$emit("collectArticleYES")
      },
      collectArticleNO() {
        this.$emit("collectArticleNO")
      },
      ok() {
        this.$emit("share",this.type,this.content)
        this.type = ""
        this.content = ""
      },
      cancel() {
        this.type = ""
        this.content = ""
      },
    },
    created() {
      var qs = require('qs')
      var self = this
      this.$http.post(this.PRIFIX + '/api/article/report/type/list').then(function (response) {
        response = response.data
        if (response.code === 0) {
          self.report = response.data.reportTypeList
        }
      }).catch(function (error) {})
    }
  }

</script>

<style lang="less" scoped>

  .share{
    margin-top: 10px;
  }

  .share-conCss{
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .img:hover{
    cursor: pointer;
  }

  .share-description{
    margin-top: 10px;
  }

  .fontCss{
    font-size: 16px;
    margin-left: 5px;
    margin-right: 2px;
    line-height: 16px;
  }

  .share-des{
    height: 16px;
  }

  .share-des:hover{
    color: #ed4040;
    cursor: pointer;
  }

  .bspan{
    display: inline-block;
  }

  .yes{
    color: #ed4040;
  }

</style>



