<template>
  <Card>
    <div class="browse">
      <h2>
        <a>近期浏览</a>
      </h2>
      <ul class="news">
        <li v-for="item in look" class="news-item">

          <a v-if="item.type==2">
            <router-link target="_blank" :to="{path:'/imgs',query:{articleId:item.id}}">
              <p>{{item.title}}</p>
            </router-link>
          </a>
          <a v-if="item.type==1">
            <router-link target="_blank" :to="{path:'/imgText',query:{articleId:item.id}}">
              <p>{{item.title}}</p>
            </router-link>
          </a>
          <a v-if="item.type==3">
            <router-link target="_blank" :to="{path:'/video',query:{articleId:item.id}}">
              <p>{{item.title}}</p>
            </router-link>
          </a>

        </li>

      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      look: Array
    }
  }
</script>

<style lang="less" scoped>

  .browse {
    width: 306px;
  }

  .news {
    margin-top: 10px;
    list-style: none;
  }

  .news-item {
    margin-top: 10px;
    padding-bottom: 2px;
    border-bottom: 1px solid #f5f5f5;
  }

  .news-item a {
    display: table;
    overflow: hidden;
    color: #222222;
    padding: 5px 0px;
  }

  .news-item a:hover {
    color: #ed4040;
  }

  .left {
    position: relative;
    overflow: hidden;
    display: inline-block;
    text-align: center;
    width: 60px;
    height: 60px;
    margin-right: 12px;
  }

  .left img {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    transition: all 0.4s;
  }

  .left img:hover {
    transform: scale(1.02);
  }

  .right {
    height: 60px;
    display: table-cell;
    vertical-align: middle;
  }

  .right p {
    font-size: 14px;
    line-height: 20px;
    max-height: 40px;
    width: 306px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

</style>



