<template>
  <Carousel class="slideshow" autoplay trigger="hover" v-model="value1" loop>
    <div v-for="(item,index) in slide">
      <!--图文-->
      <CarouselItem v-if="item.type==1">
        <router-link :to="{path:'/imgText',query:{articleId:item.id}}">
          <div class="demo-carousel">
            <img class="slideshow-img" :src="dynamicUrl+item.img[0]">
          </div>
          <p class="slideshow-title">{{item.title}}</p>
        </router-link>
      </CarouselItem>

      <!--多图-->
      <CarouselItem v-if="item.type==2">
        <router-link :to="{path:'/imgs',query:{articleId:item.id}}">
          <div class="demo-carousel">
            <img class="slideshow-img" :src="dynamicUrl+item.img[0]">
          </div>
          <p class="slideshow-title">{{item.title}}</p>
        </router-link>
      </CarouselItem>

      <!--视频-->
      <CarouselItem v-if="item.type==3">
        <router-link :to="{path:'/video',query:{articleId:item.id}}">
          <div class="demo-carousel">
            <img class="slideshow-img" :src="dynamicUrl+item.img[0]">
          </div>
          <p class="slideshow-title">{{item.title}}</p>
        </router-link>
      </CarouselItem>
    </div>
  </Carousel>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      slide: Array,
      PRIFIX: String,
      dynamicUrl: String
    },
    data () {
      return {
        value1: 0
      }
    },
    components: {}
  }
</script>

<style lang="less" scoped>
  .slideshow {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    overflow: hidden;
  }

  .slideshow-img {
    width: 100%;
    overflow:hidden;
    background-repeat: no-repeat;
    position: relative;
    border-radius: 8px;
  }

  .slideshow-title {
    position: absolute;
    bottom: 0;
    z-index: 999;
    width: 700px;
    height: 44px;
    line-height: 18px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 20px;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    background-image: linear-gradient(-180deg, transparent, rgba(0, 0, 0, .8));
    box-sizing: border-box;
  }

  .demo-carousel {
    width: 100%;
    height: 380px;
  }
</style>


