<template>
  <Card style="width: 340px" class="cc">
    <div class="personIntro">
      <!--跳转到作者页面-->
      <router-link :to="{path:'/otherPersonInfo',query:{idUser:author.userId}}">
        <div class="left">
          <img :src="author.avatar">
        </div>
      </router-link>
      <div class="right">
        <div class="name">
          <!--跳转到作者页面-->
          <router-link :to="{path:'/otherPersonInfo',query:{idUser:author.userId}}">
            <a>{{author.name}}</a>
          </router-link>
          <i v-if="author.vip==1">V认证</i>
        </div>
        <!--关注-->
        <div class="relation">
          <Button v-if="author.attention==0" size="small" icon="plus-round" type="primary"
                  @click="attentionAuthorYES">关注</Button>
          <Button v-else size="small" type="primary" disabled>已关注</Button>
        </div>
      </div>
    </div>
    <div class="article">
      <h4 class="hi">推荐图片</h4>
      <ul class="img">

        <li v-for="item in author.production" class="img-li">
          <!--跳转到多图-->
          <router-link target="_blank" :to="{path:'/imgs',query:{articleId:item.id}}">
            <a>
              <div class="img-div">
                <img :src="dynamicUrl+item.img[0]">
                <i class="more">{{item.imgNum}}图</i>
              </div>
              <p class="title">{{item.title}}</p>
            </a>
          </router-link>
        </li>

      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      author: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    methods: {
      attentionAuthorYES() {
        this.$emit("attentionAuthorYES")
      }
    }
  }

</script>

<style lang="less" scoped>

  .hi{
    font-weight: 700;
    color: #000000;
  }

  .personIntro {
    flex: 1;
    top: 0px;
    left: 25px;
    letter-spacing: 0;
  }

  .left {
    display: inline-block;
  }

  .left img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
  }

  .left img:hover {
    cursor: pointer;
  }

  .right {
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
  }

  .name {
    display: block;
    font-size: 18px;
  }

  .name a {
    color: #222222;
  }

  .name i {
    font-size: 10px;
    color: #ed4040;
    border: 1px solid #ed4040;
    padding: 0px 6px;
    border-radius: 5px;
  }

  .name a:hover {
    color: #406599;
  }

  .relation {
    display: inline-block;
    margin-top: 5px;
    margin-right: 5px;
  }

  .relation a {
    margin-top: 10px;
    font-size: 14px;
    letter-spacing: 1px;
    background-color: #2d8cf0;
    padding: 2px;
    border-radius: 3px;
    color: #ffffff;
  }

  .article li {
    padding-bottom: 4px;
    padding-top: 4px;
  }

  .img {
    display: inline-block;
    width: 312px;
    margin-top: 10px;
  }

  .img-li {
    display: inline-block;
    margin-right: 9px;
    width: 144px;
  }

  .img-li:last-child {
    margin-right: 0px;
  }

  .img-li a {
    display: inline-block;
    width: 144px;
  }

  .img-div {
    display: inline-block;
    position: relative;
    width: 144px;
    height: 84px;
    overflow: hidden;
    border-radius: 5px;
  }

  .img-div img {
    border-radius: 5px;
    display: inline-block;
    width: 144px;
    height: 84px;
    transition: all 0.4s;
  }

  .img-div img:hover {
    cursor: pointer;
    border-radius: 5px;
    transform: scale(1.05);
  }

  .more {
    position: absolute;
    font-style: normal;
    right: 8px;
    bottom: 6px;
    padding: 0 6px;
    border-radius: 10px;
    background: #000;
    filter: alpha(opacity=70);
    background: rgba(0, 0, 0, .7);
    color: #ffffff;
  }

  .title {
    margin-top: 5px;
    font-size: 14px;
    width: 144px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .article {
    margin-top: 10px;
  }


</style>



