<template>
  <Card class="card1">
    <div>
      <div class="title">
        <div class="dynamic-user">
          <div class="user-left">
            <router-link :to="{path:'/otherPersonInfo',query:{userId:dynamic.id}}">
              <img class="user-avatar" :src="dynamic.avatar">
            </router-link>
          </div>
          <div class="user-right">
            <router-link :to="{path:'/otherPersonInfo',query:{userId:dynamic.id}}">
              <p class="user-username">{{dynamic.username}}</p>
            </router-link>
            <p class="user-time">{{dynamic.time}}</p>
          </div>
        </div>
        <div class="dynamic-content">
          {{dynamic.content}}
        </div>
      </div>

      <div class="card1-left">
        <Row :gutter="16">
          <Col v-if="dynamic.imgs" v-for="(item,index) in dynamic.imgs" :key="item.id" span="6">
          <div class="img-magnify">
            <img class="card1-img" :src="dynamicUrl+item" @click="showImgs()">
          </div>
          </Col>
        </Row>
      </div>

      <div class="card1-left">
        <Row :gutter="16">
          <Col v-if="dynamic.videoList" v-for="(item,index) in dynamic.videoList" :key="item.id" span="6">
          <div class="img-magnify-video">
            <video class="card1-img" :src="dynamicUrl+item" controls="controls"
                   style="width: 100%;height: 100%;"></video>
          </div>
          </Col>
        </Row>
      </div>

      <div class="card1-right">
        <p class="card1-title"></p>
        <div class="card1-description">
          <p class="f-right">
            <span v-if="dynamic.goodFlag==0" class="ic" @click="addGood()">
              {{dynamic.good}}
              <Icon class="right1" type="thumbsup"></Icon>
            </span>
            <span v-else class="good">
              {{dynamic.good}}
              <Icon class="right1" type="thumbsup"></Icon>
            </span>
            <span @click="modal1 = true">
              <Icon class="right1 ic" type="ios-information-outline"></Icon>
            </span>
          </p>
        </div>
      </div>
    </div>

    <!--举报-->
    <Modal class="report" v-model="modal1" @on-ok="ok" @on-cancel="cancel">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>举报文章</span>
      </p>
      <RadioGroup v-model="type" vertical>
        <Radio v-for="(item,index) in report" :label="item.reportId">
          <span>{{item.reportType}}</span>
        </Radio>
        <Input v-model="val" style="width: 488px;margin-top: 10px;" type="textarea" :rows="4" placeholder="我有话说..."></Input>
      </RadioGroup>
    </Modal>

    <Modal v-model="modal101" title="相册">
      <Carousel v-model="value101" style="width: 488px">
        <CarouselItem v-for="img in dynamic.imgs" style="width: 488px">
          <div class="imgs-wrapper">
            <img :src="dynamicUrl+img">
          </div>
        </CarouselItem>
      </Carousel>
    </Modal>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      dynamic: Object,
      index: Number,
      PRIFIX: String,
      dynamicUrl: String
    },
    data() {
      return {
        modal101: false,
        value101: 0,
        modal1: false,
        type: -1,
        val: "",
        report: Array,
        imgs: []
      }
    },
    methods: {
      addGood() {
        this.$emit("addGood", this.index, this.dynamic.id)
      },
      ok() {
        this.$emit("share", this.dynamic.id, this.val, this.type)
      },
      cancel() {
        this.type = -1
        this.val = ""
      },
      showImgs () {
        this.modal101 = true
      }
    },
    created() {
      var qs = require('qs')
      var self = this
      this.$http.post(this.PRIFIX + '/api/article/report/type/list').then(function (response) {
        response = response.data
        if (response.code === 0) {
          self.report = response.data.reportTypeList
        }
      }).catch(function (error) {})
    }
  }
</script>

<style lang="less" scoped>

  .title {
    font-size: 20px;
    color: #222222;
    margin-bottom: 4px;
  }

  .card1-img {
    width: 162px;
    /*max-height: 110px;*/
    border-radius: 5px;
    display: inline-block;
    transition: all 0.4s;
    z-index: 10;
  }

  .card1-img:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  .img-magnify {
    width: 162px;
    height: 110px;
    overflow: hidden;
    margin-bottom: 10px;
    border-radius: 10px;
  }

  .img-magnify-video {
    width: 162px;
    height: 250px;
    overflow: hidden;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: #000;
  }

  .labelImg p {
    text-align: center;
    color: #ffffff;
    background-color: #ed4040;
    padding: 1px 35px;
    margin-left: -33px;
    display: inline-block;
    font-size: 14px;
    position: absolute;
    transform: rotate(-30deg);
    -ms-transform: rotate(-30deg); /* IE 9 */
    -webkit-transform: rotate(-30deg); /* Safari and Chrome */
  }

  .user-avatar {
    border-radius: 50%;
    width: 45px;
    height: 45px;
    overflow: hidden;
  }

  .user-username {
    font-size: 16px;
  }

  .user-time {
    color: #80848f;
    font-size: 14px;
  }

  .dynamic-content {
    font-size: 18px;
    margin-bottom: 8px;
  }

  .dynamic-user {
    display: flex;
    flex: 1;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
  }

  .user-right {
    margin-left: 10px;
  }

  .good {
    font-size: 18px;
    color: #ed4040;
    margin-right: 5px;
  }

  .good:hover {
    cursor: default;
  }

  .ic {
    font-size: 18px;
    margin-right: 5px;
  }

  .ic:hover {
    color: #ed4040;
    cursor: pointer;
  }

  .ic:active {
    color: #ed4040;
    animation: bounceIn 0.1s;
  }

  @keyframes bounceIn {
    0% {
      font-size: 18px;
    }
    50% {
      font-size: 22px;
    }
    100% {
      font-size: 18px;
    }
  }

  @-webkit-keyframes myShow /*Safari and Chrome*/
  {
    0% {
      font-size: 18px;
    }
    50% {
      font-size: 22px;
    }
    100% {
      font-size: 18px;
    }
  }

  .card1-right {
    height: 21px;
  }

  .card1-description {
    float: right;
  }

  .ivu-badge-count{
    right: 5px !important;
  }

  .imgs-wrapper {
    width: 100%;
    height: 35em;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  .imgs-wrapper img {
    max-width: 100%;
  }

</style>


