<template>
  <Card>

    <div class="issue-wrapper">
      <Button @click="sendDynamic" type="primary" size="large" long>发布动态</Button>
    </div>

    <Modal v-model="dynamic" title="发布动态" @on-ok="ok" @on-cancel="cancel">
      <Input v-model="content" type="textarea"
             :autosize="{minRows: 2,maxRows: 5}" placeholder="发布动态..."></Input>

      <div style="margin-top: 10px">

        <div v-if="img.length != 0" class="showImgs">
          <div v-for="v in localImgs" style="margin-right: 10px">
            <div v-if="imgsFlag">
              <Spin fix>
                <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                <div>Loading</div>
              </Spin>
            </div>
            <img :src="v" controls="controls" style="width: 9.4rem;height: 100%">
          </div>
        </div>

        <div v-if="video.length != 0" class="showImgs">
          <div v-for="(v,index) in localVideos" style="margin-right: 10px">
            <div v-if="imgsFlag">
              <Spin fix>
                <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                <div>Loading</div>
              </Spin>
            </div>
            <video :src="v" controls="controls" style="width: 9.4rem;height: 100%" :id="index"
                   @canplaythrough="myFunction(index)"></video>
          </div>
        </div>

        <div style="display: flex;align-items: center;">
          <input style="display: none" ref="uploadImg" type="file" accept="image/*" @change="getImg($event)">
          <div @click="sendImg" class="videoWrapper" style="margin-right: 10px">
            <Icon type="camera" size="20"></Icon>
          </div>

          <input style="display: none" ref="uploadVideo" type="file" accept="video/mp4" @change="getVideo($event)">
          <div @click="sendVideo" class="videoWrapper">
            <Icon type="ios-videocam" size="20"></Icon>
          </div>
        </div>
      </div>
    </Modal>
  </Card>
</template>

<script type="text/ecmascript-6">
  import {compress,returnImgData,localBase64} from '../../../common/js/compressImage'

  export default {
    props: {
      userInfo: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    data () {
      return {
        content: "",
        img: [],
        video: [],
        imgName: '',
        dynamic: false,
        localImgs: [],
        localVideos: [],
        imgsFlag: false,
        uploadVideo: []
      }
    },
    methods: {
      myFunction(id) {
        var ss = window.document.getElementById(id).duration
        if (ss > 60) {
          this.$Notice.error({
            title: '视频长度不能超过1分钟',
          })
          this.localVideos.splice(id,1)
          this.video.splice(id,1)
        } else {
          var qs = require('qs')
          var self = this
          let file = self.video[id]
          let param = new FormData(); //创建form对象
          param.append('file', file);//通过append向form对象添加数据
          let config = {
            headers: {'Content-Type': 'multipart/form-data'}
          }
          this.$http.post('https://news.immnc.com/oa/upload/mp4/' + this.userInfo.id, param, config).then(response => {
            response = response.data
            if (response.code == 0) {
              self.uploadVideo.push(response.data.videoPath)
            } else if (response.code == 3) {
              this.$Notice.error({
                title: '请登录',
              })
              let url = window.location.href + ''
              saveURL(url)
              setTimeout(function () {
                self.$router.push('/login')
              }, 500)
            } else {
              self.$toast('上传失败')
            }
          })
        }
      },
      sendVideo () {
        this.$refs.uploadVideo.click()
      },
      sendImg () {
        this.$refs.uploadImg.click()
      },
      getVideo(e) {
        if (e) {
          var self = this
          /*本地预览*/
          this.imgsFlag = true
          var reader = new FileReader()
          // 图片文件转换为base64
          reader.readAsDataURL(e.target.files[0])
          self.video.push(e.target.files[0])
          reader.onload = function(){
            // 显示图片
            self.localVideos.push(this.result)
            self.imgsFlag = false
          }
        }
      },
      getImg(e) {
        if (((e.target.files[0].size)/1024)/1024 > 5) {
          this.$Notice.error({
            title: "上传图片不能大于5M",
          })
        } else {
          /*本地预览*/
          var reader = new FileReader()
          // 图片文件转换为base64
          reader.readAsDataURL(e.target.files[0])
          reader.onload = function(){
            // 显示图片
            self.localImgs.push(this.result)
            self.imgsFlag = false
          }
          var qs = require('qs')
          var self = this
          /*压缩图片*/
          compress(e.target.files[0])
          let file
          setTimeout(function () {
            file = returnImgData()
            /*上传服务器*/
            let param = new FormData() //创建form对象
            param.append('file', file)//通过append向form对象添加数据
            let config = {
              headers: {'Content-Type': 'multipart/form-data'}
            }; //添加请求头
            self.$http.post('https://news.immnc.com/oa/upload/img/userDynamic', param, config).then(response => {
              response = response.data
              if (response.code == 0) {
                self.img.push(response.data.src)
              } else if (response.code == 3) {
                self.$Notice.error({
                  title: "请登录",
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                }, 500)
              } else {
                self.$Notice.error({
                  title: "上传失败",
                })
              }
            })
          },1000)
        }

      },
      sendDynamic() {
        this.dynamic = true
      },
      ok() {
        let sendImgs = ""
        for(let i=0;i < this.img.length;i++) {
          if(i == this.img.length-1){
            sendImgs += this.img[i]
          } else {
            sendImgs += this.img[i]+","
          }
        }
        let sendVideos = ''
        for (let i = 0; i < this.uploadVideo.length; i++) {
          if (i == this.uploadVideo.length - 1) {
            sendVideos += this.uploadVideo[i]
          } else {
            sendVideos += this.uploadVideo[i] + ","
          }
        }
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/user/dynamic/save', qs.stringify({
          'content': self.content,
          'imgUrl': sendImgs,
          'videoUrl': sendVideos,
          'userId': self.userInfo.id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.$Notice.success({
              title: "上传成功！",
            })
            window.location.reload()
          } else {
            self.$Notice.success({
              title: "上传失败",
            })
          }
        }).catch(function (error) {})
      },
      cancel() {
        return
      }
    },
  }

</script>

<style lang="less" scoped>

  .demo-upload-list{
    display: inline-block;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    margin-right: 4px;
  }
  .demo-upload-list img{
    width: 100%;
    height: 100%;
  }
  .demo-upload-list-cover{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.6);
  }
  .demo-upload-list:hover .demo-upload-list-cover{
    display: block;
  }
  .demo-upload-list-cover i{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }

  .showImgs {
    display: flex;
    align-items: center;
    width: auto;
    flex-wrap: wrap;
  }

  .videoWrapper {
    border: 1px dashed #dddee1;
    width: 58px;
    height:58px;
    line-height: 58px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .videoWrapper:hover {
    border: 1px dashed #2d8cf0;
    cursor: pointer;
  }

  .demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
  }

</style>



