<template>
  <Card style="width: 340px;margin-top: 10px" class="cc">
    <div class="model2">
        <h4 class="hi">
          幽默笑话
        </h4>
      <div class="content">
        <Spin v-if="spinShow" size="large" fix></Spin>
        <p>
          {{joke.content}}
        </p>

      </div>
      <div class="update">
        <Button @click="changeContent" type="success" icon="refresh">换一换</Button>
      </div>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      joke: Object
    },
    data() {
      return {
        spinShow: false
      }
    },
    methods: {
      changeContent() {
        this.spinShow = true
        setTimeout(() => {
          this.spinHide();
        }, 3000);
      },
      spinHide() {
        this.spinShow = false
      }
    }
  }

</script>

<style lang="less" scoped>
  .cc{
    background-color: #f8f8f9;
  }

  .hi{
    font-weight: 700;
  }

  .content {
    margin-top: 5px;
    font-size: 14px;
    color: #222222;
  }

  .update button {
    margin-top: 5px;
    width: 304px;
    font-size: 14px;
  }

</style>



