<template>
  <div>
    <!--关注-->
    <Card v-if="attentionFlag">
      <div class="attention">
        <div class="attention-item">
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.fid}}">
            <div class="avatar">
              <img :src="attentionPerson.byAvatar">
            </div>
          </router-link>
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.fid}}">
            <div class="name">
              <a>{{attentionPerson.byUsername}}</a>
            </div>
          </router-link>
        </div>
        <div v-if="attentionFlag" class="button">
          <Button v-if="userInfo.id == attentionPerson.fid" disabled>自己</Button>
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.fid}}">
            <Button v-if="userInfo.id != attentionPerson.fid" icon="plus">去看Ta</Button>
          </router-link>
        </div>
      </div>
    </Card>
    <!--粉丝-->
    <Card v-else>
      <div class="attention">
        <div class="attention-item">
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.aid}}">
            <div class="avatar">
              <img :src="attentionPerson.byAvatar">
            </div>
          </router-link>
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.aid}}">
            <div class="name">
              <a>{{attentionPerson.byUsername}}</a>
            </div>
          </router-link>
        </div>
        <div v-if="!attentionFlag" class="button">
          <Button v-if="userInfo.id == attentionPerson.aid" disabled>自己</Button>
          <Button v-if="userInfo.id != attentionPerson.aid" icon="plus">
            <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.aid}}">
              去看Ta
            </router-link>
          </Button>
        </div>
      </div>
    </Card>
  </div>

</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      modal: Boolean,
      attentionPerson: Object,
      attentionFlag: Boolean,
      index: Number,
      PRIFIX: String,
      userInfo: Object,
      dynamicUrl: String
    },
    methods: {
      showPrivateLetterModal1() {
        this.$emit("showPrivateLetterModal1",this.attentionPerson.fid)
      },
      showPrivateLetterModal2() {
        this.$emit("showPrivateLetterModal2",this.attentionPerson.aid)
      },
      attentionYES1() {
        let data1 = this.index
        let data2 = this.attentionPerson
        this.$emit("attentionYES",data1,data2)
      },
      attentionYES2() {
        let data1 = this.index
        let data2 = this.attentionPerson
        this.$emit("attentionYES",data1,data2)
      },
      attentionNO1() {
        let data1 = this.index
        this.$emit("attentionNO1",data1,this.attentionPerson)
      },
      attentionNO2() {
        let data1 = this.index
        this.$emit("attentionNO2",data1,this.attentionPerson)
      }
    }
  }

</script>

<style lang="less" scoped>

  .attention {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex: 1;
  }

  .attention-item {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .avatar img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
  }

  .avatar img:hover {
    cursor: pointer;
  }

  .name {
    font-size: 16px;
    margin-left: 20px;
  }


</style>



