<template>
  <div>
    <Card>
      <div class="message">
        <div class="content">
          <div class="label" :class="{lose:notice.reading==1}">
            通知
          </div>
          <div class="value">
            <div class="title">
              <a @click="showMessageModal">
                {{notice.title}}
              </a>
            </div>
            <div class="time">
              {{notice.time}}
            </div>
          </div>
        </div>
        <div class="del" @click="del">
          <Icon type="ios-trash"></Icon>
        </div>
      </div>
    </Card>

    <Modal v-model="modal" title="通知" :mask-closable="false" @on-ok="ok"
           :closable="false" @on-cancel="cancel">
      <p>{{notice.content}}</p>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      notice: Object,
      index: Number
    },
    data() {
      return {
        modal: false
      }
    },
    methods: {
      showMessageModal() {
        this.modal = true
        let content = this.notice
      },
      del() {
        this.$emit("delMessage",this.index)
      },
      ok() {
        this.notice.reading = 1
        this.modal = false
      },
      cancel() {
        this.notice.reading = 1
        this.modal = false
      }
    }
  }

</script>

<style lang="less" scoped>
  .message {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex: 1;
  }

  .label {
    display: inline-block;
    font-size: 12px;
    padding: 4px;
    border-radius: 5px;
    color: #ed4040;
    border: 1px solid #ed4040;
  }

  /*失效*/
  .lose {
    color: #bbbec4;
    border: 1px solid #bbbec4;
  }

  .content {
    padding-left: 5px;
  }

  .value {
    display: inline-block;
    vertical-align: middle;
    padding-left: 10px;
  }

  .title {
    width: 586px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .time {
    color: #80848f;
    font-size: 12px;
  }

  .del i:hover {
    color: #ed4040;
    cursor: pointer;
  }
</style>



