<template>
  <div class="author">
    <img class="avatar" :src="author.avatar">
    <p class="name">{{author.name}}</p>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    props: {
      author: {
        type: Object
      },
      PRIFIX: String,
      dynamicUrl: String
    }
  }
</script>

<style lang="less" scoped>
  .author {
    display: inline-block;
  }

  .author:hover {
    cursor: pointer;
  }

  .avatar {
    display: inline-block;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    vertical-align: middle;
  }

  .name {
    display: inline-block;
    font-size: 14px;
    color: #777;
    vertical-align: middle;
  }

  .name:hover {
    color: #406599;
  }
</style>



