<template>
  <Card class="card1">
    <div>
      <!--跳转到多图-->
      <router-link target="_blank" :to="{path:'/imgs',query:{articleId:content.id}}">
        <div style="width: 650px;" class="title exceed">
          {{content.title}}
        </div>
      </router-link>

      <div class="card1-left">
        <Row :gutter="16">
          <!--跳转到多图-->
          <Col v-for="(item,index) in content.img" v-if='index<=3' span="6">
          <router-link target="_blank" :to="{path:'/imgs',query:{articleId:content.id}}">
            <div class="img-magnify">
              <div v-if="index==0 && content.original==1" class="labelImg">
                <p>原创</p>
              </div>
              <img class="card1-img" :src="dynamicUrl+item">
              <i v-if="index==3" class="card1-imgNum">{{content.imgNum}}图</i>
            </div>
          </router-link>
          </Col>
        </Row>
      </div>

      <div class="card1-right">
        <p class="card1-title"></p>
        <div class="card1-description">
          <div class="card1-description-wrapper">
            <!--跳转到分类-->
            <Sort sort="多图" class="margin-right8px"></Sort>
            <Sort :sort="content.sort" class="margin-right8px"></Sort>

            <p class="margin-right8px width1"></p>
            <!--跳转到查看别人页面-->
            <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:content.author.userId}}">
              <Author :author="content.author" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" class="margin-right8px"></Author>
            </router-link>
            <span class="margin-right8px">·</span>
            <!--跳转到多图-->
            <router-link :to="{path:'/imgs',query:{articleId:content.id}}">
              <Comment :commentNum="content.commentNum" class="margin-right8px"></Comment>
            </router-link>
            <span class="margin-right8px">·</span>
            <span class="margin-right8px time">{{content.nowTime}}浏览</span>
          </div>

          <!--只有在收藏里面去删除-->
          <div v-show="collect" class="del" @click="del">
            <a>
              <Icon type="ios-trash"></Icon>
              删除
            </a>
          </div>
        </div>
      </div>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">
  import Sort from "../../../../common/components/sort/sort.vue"
  import Author from "../../../../common/components/author/author.vue"
  import Comment from "../../../../common/components/comment/comment.vue"

  export default {
    props: {
      content: Object,
      collect: Boolean,
      PRIFIX: String,
      userInfo: Object,
      dynamicUrl: String
    },
    data() {
      return {
        articleId: this.content.id,
        userId: this.content.author.id
      }
    },
    components: {
      Sort,
      Author,
      Comment
    },
    methods: {
      del() {
        this.$emit("delArticle", this.content.collectId, this.content.type)
      }
    }
  }
</script>

<style lang="less" scoped>

  .width1 {
    display: inline-block;
    height: 10px;
    width: 5px;
  }

  .title {
    font-size: 20px;
    color: #222222;
    margin-bottom: 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: 700;
  }

  .card1-description{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .card1-description-wrapper {
    display: flex;
    align-items: center;
  }

  .margin-right8px{
    margin-right: 6px;
  }

  .card2Wrapper {
    display: table;
  }

  .card2-title {
    display: inline-block;
    font-size: 20px;
    color: #222222;
    height: 60px;
    margin-bottom: 4px;
    overflow: hidden;
  }

  .title:hover, .card2-title:hover {
    color: #406599;
    cursor: pointer;
  }

  .card1-img {
    max-width: 156px;
    min-height: 110px;
    border-radius: 5px;
    display: inline-block;
    transition: all 0.6s;
    z-index: 10;
  }

  .card1-img:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  .card1-imgNum {
    position: absolute;
    font-style: normal;
    right: 15px;
    bottom: 15px;
    padding: 0px 6px;
    border-radius: 10px;
    font-size: 12px;
    color: #ffffff;
    height: 22px;
    line-height: 22px;
    background-color: rgba(0, 0, 0, 0.7);
  }

  .card1-imgNum:hover {
    cursor: pointer;
  }

  .img-magnify {
    width: 156px;
    height: 110px;
    overflow: hidden;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: #000000;
    text-align: center;
  }

  .labelImg {
    position: absolute;
    width: 156px;
    height: 110px;
    overflow: hidden;
    transition: all 0.6s;
    z-index: 11;
  }

  .labelImg:hover {
    cursor: pointer;
    transform: scale(1.0);
  }

  .labelImg:hover + .card1-img {
    transform: scale(1.1);
  }

  .labelImg p {
    text-align: center;
    color: #ffffff;
    background-color: #ed4040;
    padding: 1px 35px;
    margin-left: -33px;
    display: inline-block;
    font-size: 14px;
    position: absolute;
    transform: rotate(-30deg);
    -ms-transform: rotate(-30deg); /* IE 9 */
    -webkit-transform: rotate(-30deg); /* Safari and Chrome */
  }

  .del {
    display: inline-block;
    float: right;
  }

  .del a {
    color: #777;
  }

  .del a:hover {
    color: #ed4040;
  }

</style>


