<template>
  <Card style="width: 340px" class="cc">
    <div class="personIntro">
      <!--跳转到个人中心-->
      <router-link target="_blank" to="/personInfo">
        <div class="left">
          <img :src="userInfo.avatar">
        </div>
      </router-link>
      <div class="right">
        <div class="name">
          <!--跳转到个人中心-->
          <router-link target="_blank" to="/personInfo">
            <a>{{userInfo.name}}</a>
          </router-link>
        </div>
        <div class="relation">
          <!--跳转到个人中心-->
          <router-link target="_blank" to="/personInfo">
            <a>关注:{{userInfo.attention}}</a>
          </router-link>
        </div>
        <div class="relation">
          <!--跳转到个人中心-->
          <router-link target="_blank" to="/personInfo">
            <a>粉丝:{{userInfo.fans}}</a>
          </router-link>
        </div>
      </div>
      <!--跳转到个人中心-->
      <a @click="outLogin" class="login">退出登录</a>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">
  import {clearToLocal} from "../../../../../src/common/js/store.js"

  export default {
    props: {
      userInfo: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    methods: {
      outLogin() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/user/logout',qs.stringify({
          'userId': self.userInfo.id
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            clearToLocal()
            self.$Notice.success({
              title: "退出成功"
            })
            this.$router.push('/home')
          } else {
            self.$Notice.error({
              title: "退出失败"
            })
          }
        }).catch(function (error) {
          window.location.reload()
        })

      }
    }
  }

</script>

<style lang="less" scoped>
  .personIntro {
    flex: 1;
    top: 0px;
    left: 25px;
    letter-spacing: 0;
  }

  .left {
    display: inline-block;
  }

  .left img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
  }

  .left img:hover {
    cursor: pointer;
  }

  .right {
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
  }

  .name {
    display: block;
    font-size: 18px;
  }

  .name a {
    color: #222222;
  }

  .name a:hover {
    color: #ed4040;
  }

  .relation {
    display: inline-block;
    margin-right: 5px;
  }

  .relation a {
    font-size: 14px;
    color: #222;
  }

  .relation a:hover {
    color: #ed4040;
  }

  .login {
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 12px;
  }

</style>



