<template>
  <Modal v-model="modal" title="私信聊天室" @on-ok="ok" @on-cancel="cancel"
         :styles="{top: '20px'}">
    <div class="content">

      <Scroll :on-reach-top="handleReachTop" height="527">

      <ul ref="privateLetter">

        <!--<li class="you">
          <div class="avatar">
            <img src="https://p9.pstatp.com/large/39fd00008f4757fe8cc1">
          </div>
          <div class="message">
            <h4 class="name">小可爱</h4>
            <p class="value">
              阿里老司机带你上路运维开发《Python自动化运维项目实战》深度应用Django，从项目实践角度出发，
              围绕自动化资产扫描和发现、Ansible自动化任务执行的内容展开~打造高逼格的资产扫描、
              自动化任务的DevOps项目,祝你晋升Python DevOps工程师！别说话快上车：https://coding.imooc.com/class/160.html
            </p>
            <p class="time">2018-01-11 18:26</p>
          </div>
        </li>-->

        <li :class="judge(chat.id)" v-for="(chat,index) in chatMessages">
          <div v-if="ifYou(chat.id)" class="avatar">
            <img :src="chat.avatar">
          </div>
          <div class="message">
            <h4 class="name">{{chat.name}}</h4>
            <div ref="value" class="value">
              {{chat.message}}
            </div>
            <p class="time">{{chat.time}}</p>
          </div>
          <div v-if="ifMe(chat.id)" class="avatar">
            <img :src="chat.avatar">
          </div>
        </li>

      </ul>
      </Scroll>
    </div>
    <div class="footer" slot="footer">
      <!--<emoji-icon @select="selectIcon"></emoji-icon>-->
      <div ref="chatRef" class="chat-value" v-html="user.sendMessage" contenteditable="true"></div>
      <Button type="info" @click="send">发送</Button>
    </div>
  </Modal>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      modal: Boolean,
      userInfo: Object,
      issueId: String,
      PRIFIX: String,
      chatMessages: Array,
      dynamicUrl: String
    },
    data() {
      return {
        user: {
          id: this.userInfo.id,
          username: this.userInfo.name,
          avatar: this.userInfo.avatar,
          sendMessage: ''
        }
      }
    },
    methods: {
      handleReachTop() {
        return
      },
      scrollButtom() {
        this.$refs.privateLetter.scrollIntoView(false)
      },
      judge(id) {
        /*假设当前用户id为1*/
        if (id == this.user.id) {
          return "me"
        } else {
          return "you"
        }
      },
      ifYou(id) {
        /*假设当前用户id为1*/
        if (id != this.user.id) {
          return true
        }
      },
      ifMe(id) {
        /*假设当前用户id为1*/
        if (id == this.user.id) {
          return true
        }
      },
      ok () {
        this.$emit("hidePrivateLetterModal")
        return
      },
      cancel () {
        this.$emit("hidePrivateLetterModal")
        return
      },
      selectIcon(val) {
        this.$refs.chatRef.innerHTML += val
        this.user.sendMessage = this.$refs.chatRef.innerHTML
      },
      send() {
        /*判断是否输入空值*/
        if(this.$refs.chatRef.innerHTML == ""){
          this.$Message.error('不能发送内容为空的消息！');
          return
        }

        /*赋值发送时间*/
        let myDate = new Date();
        let year = myDate.getFullYear();    //获取完整的年份(4位,1970-????)
        let month = myDate.getMonth();       //获取当前月份(0-11,0代表1月)
        let day = myDate.getDate();        //获取当前日(1-31)
        let hours = myDate.getHours();       //获取当前小时数(0-23)
        let min = myDate.getMinutes();     //获取当前分钟数(0-59)
        let nowDate = year + '年' + month + "月" + day + "日" + hours + "时" + min + "分"

        /*发送信息赋值*/
        let sendMessage = {
          id: this.user.id,
          avatar: this.user.avatar,
          username: this.user.username,
          time: nowDate,
          message: this.$refs.chatRef.innerHTML
        }
        let l = this.chatMessages.length
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/communication/save',qs.stringify({
          'uid': self.issueId,
          'sendMessage': self.$refs.chatRef.innerHTML
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.chatMessages.push(sendMessage)
            self.$nextTick(() => {
              for (let i = l+1; i<=self.chatMessages.length; i++){
                self.$refs.value[i-1].innerHTML = self.$refs.value[i-1].innerText
              }
              /*清空输入框*/
              self.$refs.chatRef.innerHTML= ""
              /*滚动到底部*/
              self.scrollButtom()
            })
          } else if (response.code == 3) {
            self.$Notice.error({
              title: response.message,
            })
            self.$router.push("/login")
          } else {
            self.$Notice.error({
              title: '发送失败',
            })
          }
        }).catch(function (error) {})
      },
      conversionHTML() {
        let text = this.$refs.value
        if(text) {
          text.forEach((t) => {
            t.innerHTML = t.innerText
          })
        }
      }
    },
    watch: {
      chatMessages: function () {
        this.$nextTick(function () {
          this.conversionHTML()
          this.scrollButtom()
        })
      }
    }
  }
</script>

<style lang="less" scoped>

  .content {
    height: 527px;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .you {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 20px;
  }

  .avatar {
    width: 40px;
    height: 40px;
  }

  .avatar img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }

  .you > .avatar {
    margin-left: 5px;
  }

  .me > .avatar {
    margin-right: 5px;
  }

  .you > .message {
    margin-left: 15px;
  }

  .you > .message > .name {
    float: left;
    margin-bottom: 5px;
  }

  .you > .message > .value {
    clear: both;
  }

  .value {
    position: relative;
    padding: 10px;
    width: 355px;
    border-radius: 10px;
    background-color: #f8f8f9;
    color: #1c2438;
    font-size: 14px;
    margin-bottom: 5px;
  }

  .you > .message > .time {
    color: #80848f;
    float: right;
    margin-right: 10px;
  }

  .me {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    margin-bottom: 20px;
  }

  .me > .message {
    margin-right: 15px;
  }

  .me > .message > .name {
    float: right;
    margin-bottom: 5px;
  }

  .me > .message > .value {
    clear: both;
  }

  .me > .message > .time {
    color: #80848f;
    float: left;
    margin-left: 10px;
  }

  .footer {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .chat-value {
    /*width: 370px;*/
    width: 400px;
    min-height: 25px;
    max-height: 70px;
    padding: 5px 5px;
    border: 1px solid #dddee1;
    text-align: left;
    margin: 0px 10px;
    border-radius: 4px;
    color: #495060;
    cursor: text;
    overflow-y: auto;
    font-size: 15px;
    background-color: #f8f8f9;
  }

  .chat-value:focus {
    outline: none;
  }

</style>



