<template>
  <div class="comment">{{num}}评论</div>
</template>

<script type="text/ecmascript-6">
  export default{
    props: {
      commentNum: {
        type: Number
      }
    },
    data() {
      return {
        num: this.commentNum + "条"
      }
    }
  }
</script>

<style lang="less" scoped>
  .comment{
    display: inline-block;
    vertical-align:middle;
  }

  .comment:hover{
    color: #406599;
    cursor:pointer;
  }
</style>



