<template>
  <div ref="el" class="content">
    <div class="left">
      <div style="margin-top: 10px" v-if="loadCss" class="demo-spin-col">
        <Spin fix>
          <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </div>
      <BodyContent v-if="!loadCss" :article="sortContent" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :contentFlag="contentFlag"></BodyContent>
      <div v-if="loadCssFlag" class="demo-spin-col">
        <Spin fix>
          <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </div>
    </div>
    <div class="right">
      <div ref="elr">
        <!--登录之后-->
        <PersonIntro v-if="userInfo.flag" :userInfo="userInfo" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></PersonIntro>
        <!--登录-->
        <Login v-else></Login>
        <!--推荐模块-->
        <RecommendModel :recommend="recommendList" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></RecommendModel>
      </div>

      <Affix v-if="mo" :offset-top="65">
        <model1 :PRIFIX="PRIFIX" :realTimeNews="recommendList.realTimeNews" :dynamicUrl="dynamicUrl"></model1>
      </Affix>
    </div>
    <!--用户-->
    <UserLogin :userInfo="userInfo" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></UserLogin>
    <BackTop></BackTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import SlideShow from "../index/components/slideshow/slideshow.vue"
  import PersonIntro from "../index/components/personIntro/personIntro.vue"
  import RecommendModel from "../index/components/recommendModel/recommendModel.vue"
  import Login from "../index/components/personIntro/login.vue"
  import BodyContent from "../index/components/body/body.vue"
  import {readLocal} from "../../../src/common/js/store.js"
  import model1 from "../index/components/recommendModel/model1/model1.vue"
  import UserLogin from "../../views/common/components/userLogin/userLogin.vue"

  export default {
    props: {
      userInfo: Object,
      recommendList: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    data() {
      return {
        contentFlag: false,
        navType: -1,
        flag: false,
        /*分类内容*/
        sortContent: [],
        mo: false,
        i: 2,
        loadCssFlag: false,
        loadCss: false
      }
    },
    watch: {
      navType: function () {
        var qs = require('qs')
        var self = this
        self.loadCss = true
        setTimeout(function () {
          self.loadCss = false
        },500)
        this.$http.post(this.PRIFIX + '/api/homePage/type/article', qs.stringify({
          'page': 1,
          'size': 10,
          'type': self.navType
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            for(let i=0; i< response.data.article.pageContent.length;i++) {
              self.$set(self.sortContent,i,response.data.article.pageContent[i])
            }
          }
        }).catch(function (error) {})
      }
    },
    components: {
      SlideShow,
      PersonIntro,
      RecommendModel,
      Login,
      BodyContent,
      model1,
      UserLogin
    },
    mounted () {
      this.$nextTick(() => {
        window.addEventListener('scroll', () => {
          /*内容高度*/
          let elHeight = this.$refs.el.clientHeight + 80
          /*获取左边内容高度*/
          let elrHeight = this.$refs.elr.clientHeight + 50 + window.innerHeight
          /*滚动高度*/
          let height = 0
          if (document.body.scrollTop) {
            height = document.body.scrollTop
          }
          if (document.documentElement.scrollTop) {
            height = document.documentElement.scrollTop
          }
          /*右边滚动显示推荐*/
          if(height + window.innerHeight >= elrHeight) {
            this.mo = true
          } else {
            this.mo = false
          }
          if (height + window.innerHeight >= elHeight) {
            if(!this.contentFlag) {
              /*推送文章*/
              var qs = require('qs')
              var self = this
              self.loadCssFlag = true
              this.$http.post(this.PRIFIX + '/api/homePage/type/article', qs.stringify({
                'page': self.i,
                'size': 10,
                'type': self.navType
              })).then(function (response) {
                response = response.data
                if (response.code == 0) {
                  if(response.data.article.pageContent.length == 0) {
                    self.contentFlag = true
                    self.loadCssFlag = false
                  } else {
                    response.data.article.pageContent.forEach((item) => {
                      self.sortContent.push(item)
                      self.loadCssFlag = false
                    })
                  }
                }
              }).catch(function (error) {})
              self.i++
            }
          }
        })
      })
    },
    created() {
      this.userInfo = readLocal()
      this.navType = this.$route.query.navType
    }
  }
</script>

<style lang="less" scoped>
  .content {
    display: flex;
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .left {
    flex: 0 0 710px;
    width: 710px;
    margin-right: 20px;
    margin-top: -10px;
  }

  .right {
    flex: 1;
    width: 340px;
  }

  .card {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 10px #999;
    -moz-box-shadow: 0 0 10px #999;
    box-shadow: 0 0 10px #999;
  }

  .go {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 25px #999;
    -moz-box-shadow: 0 0 25px #999;
    box-shadow: 0 0 25px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveGo 0.5s;
  }

  .to {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 10px #999;
    -moz-box-shadow: 0 0 10px #999;
    box-shadow: 0 0 10px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveTo 0.5s;
  }

  @keyframes moveGo {
    from {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
    to {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
  }

  @-webkit-keyframes moveGo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
    to {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
  }

  @keyframes moveTo {
    from {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
    to {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
  }

  @-webkit-keyframes moveTo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
    to {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
  }

</style>



