<template>
  <div>
    <!--作者入驻-->
    <BackTop :height="0" :bottom="170" @on-click="goLoginAuthor">
      <Tooltip content="作者入驻" placement="left">
        <div class="loginCommon">
          <Icon type="person-add"></Icon>
        </div>
      </Tooltip>
    </BackTop>
    <!--未登录状态-->
    <div v-if="!userInfo.flag">
      <BackTop :height="0" :bottom="100" @on-click="goLogin">
        <router-link to="/login">
          <div class="loginCommon">
            <Tooltip content="登录" placement="left">
              <Icon type="log-in"></Icon>
            </Tooltip>
          </div>
        </router-link>
      </BackTop>
    </div>
    <!--已登录状态-->
    <div v-else>
      <BackTop :height="0" :bottom="100" @on-click="showUser">
        <router-link target="_blank" to="/personInfo">
          <Tooltip content="个人中心" placement="left">
            <div class="showUser">
              <img :src="userInfo.avatar">
            </div>
          </Tooltip>
        </router-link>
      </BackTop>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      userInfo: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    methods: {
      goLogin() {
      },
      showUser() {
      },
      goLoginAuthor() {
        if (this.userInfo.flag == false) {
          this.$router.push('/login')
        } else {
          this.$router.push('/personInfo')
        }
      }
    }

  }

</script>

<style lang="less" scoped>

  .loginCommon {
    border-radius: 50%;
    background-color: rgba(0, 0, 0, .6);
    color: #ffffff;
    text-align: center;
    padding: 3px 0px;
    font-size: 16px;
    transition: all .2s ease-in-out;
  }

  .loginCommon:hover {
    background-color: rgba(0, 0, 0, .7);
  }

  .showUser img {
    border-radius: 50%;
    width: 46px;
    height: 46px;
    transition: all .2s ease-in-out;
  }


</style>



