<template>
  <div>

    <div class="top_wrapper">
      <Card style="width: 100%;">
        <div class="top">
          <div class="person-top-wrapper">
            <div class="person-top-background">
              <div class="upload-background">
                <Upload action="https://news.immnc.com/oa/upload/img/user"
                        :on-success="handleSuccess1" :format="['jpg','jpeg','png']"
                        :on-format-error="handleFormatError"
                        :max-size="2048" :on-exceeded-size="handleMaxSize">
                  <Icon type="camera"></Icon>
                  <span>上传背景图</span>
                </Upload>
