            <img v-else src="../../assets/img/appBcg.jpg" ref="img" class="person-top filter2px">
          </div>
          <div class="person-bottom">
            <div class="avatar">
              <div class="avatar-background">
                <Upload action="https://news.immnc.com/oa/upload/img/user"
                        :on-success="handleSuccess2" :format="['jpg','jpeg','png']"
                        :on-format-error="handleFormatError"
                        :max-size="2048" :on-exceeded-size="handleMaxSize">
                  <Icon type="camera"></Icon>
                  <span>上传头像</span>
                </Upload>
              </div>
              <img :src="userInfo.avatar">
            </div>
            <h2 class="big-name">{{userInfo.username}}</h2>
          </div>
          <!--修改内容-->
          <div class="person-content">
            <Form ref="formValidate" :model="formValidate" :label-width="80">
              <FormItem label="昵称" prop="name">
                <Input v-model="formValidate.name" placeholder="我们该如何称呼您"></Input>
              </FormItem>
              <FormItem label="密码" prop="password">
                <Button class="password-button" type="ghost" @click="modal1 = true">
                  修改密码
                </Button>
              </FormItem>
              <FormItem v-if="userInfo.phone" label="修改手机号" prop="password">
                <Button class="password-button" type="ghost" @click="modal3 = true">
                  修改手机号
                </Button>
              </FormItem>
              <FormItem v-else label="绑定手机号" prop="password">
                <Button class="password-button" type="ghost" @click="modal2 = true">
                  绑定手机号
                </Button>
              </FormItem>
              <FormItem label="QQ" prop="qq">
                <Input v-model="formValidate.QQ" placeholder="您的QQ号码"></Input>
              </FormItem>
              <!--<FormItem label="电话" prop="phone">
                <Button class="password-button" type="ghost" @click="modal2 = true">
                  修改手机号码
                </Button>
              </FormItem>-->
              <FormItem label="电子邮箱" prop="mail">
                <Input v-model="formValidate.email" placeholder="您的邮箱地址"></Input>
              </FormItem>
              <FormItem label="所在城市" prop="city">
                <Input v-model="formValidate.city" placeholder="您所在的城市"></Input>
              </FormItem>
              <FormItem label="性别" prop="gender">
                <RadioGroup v-model="formValidate.sex">
                  <Radio label="1">小哥哥{{formValidate.sex}}</Radio>
                  <Radio label="0">小姐姐</Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="生日">
                <DatePicker type="date" placeholder="生日" v-model="formValidate.birthday"></DatePicker>
              </FormItem>
              <FormItem label="个性签名" prop="desc">
                <Input v-model="formValidate.signature" placeholder="您的个性签名"></Input>
              </FormItem>
              <FormItem>
                <Button type="primary" @click="handleSubmit('formValidate',4)">提交</Button>
                <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px">
                  重置
                </Button>
              </FormItem>
            </Form>
          </div>
        </div>
      </Card>

      <Modal v-model="modal1" title="修改密码" :mask-closable="false" :closable="false" @on-ok="ok1"
             @on-cancel="cancel1">
        <Form :model="formValidatePassword" ref="updatePassword" :rules="ruleValidatePassword">
          <FormItem label="旧密码" prop="password">
            <Input v-model="formValidatePassword.password" placeholder="请输入之前的密码"></Input>
          </FormItem>
          <FormItem label="验证码" prop="validationCode">
            <div class="inputContent">
              <Input v-model="formValidatePassword.validationCode" class="inputCss" type="text"
                     placeholder="输入验证码..."/>
              <div class="inputButton">
                <Button type="ghost" @click="getCode2" :disabled="disabled">{{timeCode}}</Button>
              </div>
            </div>
          </FormItem>
          <FormItem label="新密码" prop="password1">
            <Input v-model="formValidatePassword.password1" placeholder="请输入新的密码"></Input>
          </FormItem>
          <FormItem label="确认密码" prop="password2">
            <Input v-model="formValidatePassword.password2" placeholder="请再次输入密码"></Input>
          </FormItem>
        </Form>
      </Modal>

      <Modal v-model="modal2" title="绑定手机号码" :mask-closable="false" :closable="false" @on-ok="ok2"
             @on-cancel="cancel2">
        <Form :model="formValidatePhone" ref="updatePhone" :rules="ruleValidatePhone">
          <FormItem label="手机号码" prop="phone">
            <Input type="text" v-model="formValidatePhone.phone" placeholder="请输入手机号码"></Input>
          </FormItem>
          <FormItem label="验证码" prop="validationCode">
            <div class="inputContent">
              <Input v-model="formValidatePhone.validationCode" class="inputCss" type="text"
                     placeholder="输入验证码..."/>
              <div class="inputButton">
                <Button type="ghost" @click="getCode1" :disabled="disabled">{{timeCode}}</Button>
              </div>
            </div>
          </FormItem>
        </Form>
      </Modal>

      <Modal v-model="modal3" title="修改手机号码" :mask-closable="false" :closable="false" @on-ok="ok3"
             @on-cancel="cancel3">
        <Form :model="formValidatePhone" ref="updatePhone" :rules="ruleValidatePhone">
          <FormItem label="手机号码" prop="phone">
            <Input type="text" v-model="formValidatePhone.phone" placeholder="请输入手机号码"></Input>
          </FormItem>
          <FormItem label="旧手机号码" prop="phone">
            <Input type="text" v-model="formValidatePhone.oldPhone" placeholder="请输入手机号码"></Input>
          </FormItem>
          <FormItem label="验证码" prop="validationCode">
            <div class="inputContent">
              <Input v-model="formValidatePhone.validationCode" class="inputCss" type="text"
                     placeholder="输入验证码..."/>
              <div class="inputButton">
                <Button type="ghost" @click="getCode" :disabled="disabled">{{timeCode}}</Button>
              </div>
            </div>
          </FormItem>
        </Form>
      </Modal>

    </div>
    <BackTop></BackTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bmodel1 from "../index/components/body/model1/model1.vue"
  import Bmodel2 from "../index/components/body/model2/model2.vue"
  import Model1 from "../index/components/recommendModel/model1/model1.vue"
  import Model3 from "../index/components/recommendModel/model3/model3.vue"
  import UserLogin from "../common/components/userLogin/userLogin.vue"
  import Browse from "./components/browse.vue"
  import Attention from "./components/attention.vue"
  import Issue from "./components/issue.vue"
  import AttentionPerson from "../common/components/attention/attention.vue"
  import Message from "../common/components/message/message.vue"
  import {saveToLocal} from "../../../src/common/js/store.js"
  import {readLocal} from "../../../src/common/js/store.js"

  export default {
    props: {
      PRIFIX: String,
      dynamicUrl: String,
      userInfo: Object
    },
    data () {
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入您的密码'));
        } else if (value !== this.formValidatePassword.password1) {
          callback(new Error('2次密码不一致!'));
        } else {
          callback();
        }
      }
      const validatePassYES = (rule, value, callback) => {
        var flag = false
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/user/checkPassword', qs.stringify({
          'userId': self.userInfo.id,
          'password': self.formValidatePassword.password
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            flag = true
            if (!flag) {
              callback(new Error('密码错误!'));
            } else {
              callback();
            }
          } else {
            self.$Notice.error({
              title: response.message,
            })
          }
        }).catch(function (error) {})
      }
      const validateCode = (rule, value, callback) => {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/user/checkCode', qs.stringify({
          'code': value
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            callback();
          } else {
            callback(new Error('验证码错误!'));
          }
        }).catch(function (error) {})
      }
      return {
        modal1: false,
        modal2: false,
        modal3: false,
        passwordFlag: false,
        timeCode: "获取验证码",
        time: 60,
        disabled: false,
        formValidate: {
          QQ: this.userInfo.QQ,
          sex: this.userInfo.sex,
          email: this.userInfo.email,
          city: this.userInfo.city,
          birthday: this.userInfo.birthday,
          signature: this.userInfo.signature,
          name: this.userInfo.name,
        },
        formValidatePassword: {
          oldPassword: "",
          password: '',
          password1: '',
          password2: '',
          validationCode: '',
        },
        formValidatePhone: {
          phone: '',
          oldPhone: '',
          validationCode: '',
        },
        ruleValidate: {
          name: [
            {required: true, message: '请输入您的昵称', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '请输入您的邮箱', trigger: 'blur'},
            {type: 'email', message: '邮箱格式错啦！', trigger: 'blur'}
          ],
          QQ: [
            {required: true, message: '请输入您的QQ', trigger: 'blur'},
            {type: 'string', max: 11, message: '现在qq可没有12位以上的哟！', trigger: 'blur'}
          ],
          city: [
            {required: true, message: '请输入您所在的城市', trigger: 'blur'}
          ],
          sex: [
            {required: true, message: '您是小哥哥还是小姐姐呢？', trigger: 'change'}
          ],
          signature: [
            {required: true, message: '让别人了解了解你吧', trigger: 'blur'},
            {type: 'string', max: 50, message: '不能超过50个字哟~', trigger: 'blur'}
          ],
          birthday: [
            {required: true, message: '请输入生日哟~', trigger: 'blur'},
          ]
        },
        ruleValidatePassword: {
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {type: 'string', min: 6, message: '不能少于6位密码哟~', trigger: 'blur'},
            {validator: validatePassYES, trigger: 'blur'}
          ],
          password1: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {type: 'string', min: 6, message: '不能少于6位密码哟~', trigger: 'blur'}
          ],
          password2: [
            {type: 'string', min: 6, message: '不能少于6位密码哟~', trigger: 'blur'},
            {validator: validatePassCheck, trigger: 'blur'}
          ],
          validationCode: [
            {required: true, message: '请输入验证码', trigger: 'blur'},
            {validator: validateCode, trigger: 'blur'}
          ]
        },
        ruleValidatePhone: {
          phone: [
            {required: true, message: '请输入您的电话', trigger: 'blur'},
            {type: 'string', min: 11, max: 11, message: '电话号码错了哟~', trigger: 'blur'}
          ],
          oldPhone: [
            {required: true, message: '请输入您的电话', trigger: 'blur'},
            {type: 'string', min: 11, max: 11, message: '电话号码错了哟~', trigger: 'blur'}
          ],
          validationCode: [
            {required: true, message: '请输入验证码', trigger: 'blur'},
            {validator: validateCode, trigger: 'blur'}
          ]
        },
        avatar: "",
        backgroundImg: "",
      }
    },
    components: {
      Bmodel1,
      Bmodel2,
      Model1,
      Model3,
      UserLogin,
      Browse,
      Attention,
      Issue,
      AttentionPerson,
      Message
    },
    methods: {
      ok1 () {
        this.handleSubmit('updatePhone',1)
      },
      cancel1 () {
        this.password1 = ''
        this.password2 = ''
        this.$Message.info('取消修改！')
        this.handleReset('updatePassword')
      },
      ok2 () {
        this.handleSubmit('updatePhone',2)
      },
      cancel2 () {
        this.password1 = ''
        this.password2 = ''
        this.$Message.info('取消修改！')
        this.handleReset('updatePhone')
      },
      ok3 () {
        this.handleSubmit('updatePhone',3)
      },
      cancel3 () {
        this.password1 = ''
        this.password2 = ''
        this.$Message.info('取消修改！')
        this.handleReset('updatePhone')
      },
      handleSubmit (name,type) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if (type == 1) {
              var qs = require('qs')
              var self = this
              this.$http.post(this.PRIFIX +'/api/user/updatePassword', qs.stringify({
                'userId': self.userInfo.id,
                'code': self.formValidatePassword.validationCode,
                'newPassword': self.formValidatePassword.password2,
                'oldPassword': self.formValidatePassword.password
              })).then(function (response) {
                response = response.data
                if (response.code === 0) {
                  self.$Notice.success({
                    title: '修改成功',
                  })
                } else {
                  self.$Notice.error({
                    title: '修改失败',
                  })
                }
              }).catch(function (error) {})
            }
            if (type == 2) {
              var qs = require('qs')
              var self = this
              this.$http.post(self.PRIFIX + 'api/user/bindingPhone',qs.stringify({
                'userId': self.userInfo.id,
                'phone':self.formValidatePhone.phone,
                'code': self.formValidatePhone.validationCode
              })).then(function (response) {
                response = response.data
                if (response.code === 0) {
                  self.$Notice.success({
                    title: '修改成功',
                  })
                  window.location.reload()
                } else {
                  self.$Notice.error({
                    title: response.message,
                  })
                }
              }).catch(function (error) {})
            }
            if (type == 3) {
              var qs = require('qs')
              var self = this
              this.$http.post(self.PRIFIX + 'api/user/updatePhone',qs.stringify({
                'phone': self.formValidatePhone.phone,
                'oldPhone': self.formValidatePhone.oldPhone,
                'code': self.formValidatePhone.validationCode
              })).then(function (response) {
                response = response.data
                if (response.code === 0) {
                  self.$Notice.success({
                    title: '修改成功',
                  })
                  window.location.reload()
                } else {
                  self.$Notice.error({
                    title: "修改失败",
                  })
                }
              }).catch(function (error) {})
            }
            if (type == 4) {
              var qs = require('qs')
              var self = this
              this.$http.post(self.PRIFIX + '/api/personal/user/update',qs.stringify({
                'QQ':self.formValidate.QQ,
                'sex':self.formValidate.sex,
                'email':self.formValidate.email,
                'city': self.formValidate.city,
                'birthday': self.formValidate.birthday,
                'signature': self.formValidate.signature,
                'nikeName': self.formValidate.name
              })).then(function (response) {
                response = response.data
                if (response.code === 0) {
                  saveToLocal(
                    self.userInfo.id,
                    self.formValidate.name,
                    self.userInfo.avatar,
                    self.formValidate.QQ,
                    self.formValidate.sex,
                    self.formValidate.email,
                    self.formValidate.city,
                    self.formValidate.birthday,
                    self.formValidate.signature,
                    self.userInfo.backgroundImg,
                    self.userInfo.phone,
                    self.userInfo.fans,
                    self.userInfo.attention,
                    true
                  )
                  self.$Notice.success({
                    title: '修改成功',
                  })
                } else {
                  self.$Notice.error({
                    title: "修改失败",
                  })
                }
              }).catch(function (error) {})
            }
          } else {
            this.$Notice.error({
              title: '修改失败',
            })
          }
        })

      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      vilidatePassword() {

      },
      showPassword() {
        this.passwordFlag = !this.passwordFlag
      },
      getCode1() {
        if(this.formValidatePhone.phone == '') {
          this.$Notice.error({
            title: '请输入手机号',
          })
          return
        }
        if (this.time > 0) {
          if (this.time == 60) {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/user/checkPhoneToCode', qs.stringify({
              'phone': self.formValidatePhone.phone,
              'type': 1
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.$Notice.success({
                  title: '验证码已发送',
                })
              } else {
                self.$Notice.error({
                  title: '验证码发送失败',
                })
              }
            }).catch(function (error) {})
          }
          this.time--
          this.timeCode = this.time + "s 后重新获取"
          this.disabled = true
          setTimeout(this.getCode, 1000)
        } else {
          this.time = 60
          this.disabled = false
          this.timeCode = "获取验证码"
        }
      },
      getCode() {
        if(this.formValidatePhone.phone == '') {
          this.$Notice.error({
            title: '请输入手机号',
          })
          return
        }
        if (this.time > 0) {
          if (this.time == 60) {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/user/checkPhoneToCode', qs.stringify({
              'phone': self.formValidatePhone.phone,
              'type': 0
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.$Notice.success({
                  title: '验证码已发送',
                })
              } else {
                self.$Notice.error({
                  title: '验证码发送失败',
                })
              }
            }).catch(function (error) {})
          }
          this.time--
          this.timeCode = this.time + "s 后重新获取"
          this.disabled = true
          setTimeout(this.getCode, 1000)
        } else {
          this.time = 60
          this.disabled = false
          this.timeCode = "获取验证码"
        }
      },
      getCode2() {
        if (this.time > 0) {
          if (this.time == 60) {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/user/checkPhoneToCode', qs.stringify({
              'phone': self.userInfo.phone,
              'type': 0
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.$Notice.success({
                  title: '验证码已发送',
                })
              } else {
                self.$Notice.error({
                  title: '验证码发送失败',
                })
              }
            }).catch(function (error) {})
          }
          this.time--
          this.timeCode = this.time + "s 后重新获取"
          this.disabled = true
          setTimeout(this.getCode, 1000)
        } else {
          this.time = 60
          this.disabled = false
          this.timeCode = "获取验证码"
        }
      },
      handleSuccess1 (res, file) {
        if(file.response.code == 0) {
          var qs = require('qs')
          var self = this
          this.$http.post(self.PRIFIX + '/api/personal/user/update', qs.stringify({
            'backgroundImg': file.response.data.src,
            'userId': self.userInfo.id
          })).then(function (response) {
            response = response.data
            if (response.code == 0) {
              saveToLocal(
                self.userInfo.id,
                self.formValidate.name,
                self.userInfo.avatar,
                self.formValidate.QQ,
                self.formValidate.sex,
                self.formValidate.email,
                self.formValidate.city,
                self.formValidate.birthday,
                self.formValidate.signature,
                file.response.data.src,
                self.userInfo.phone,
                self.userInfo.fans,
                self.userInfo.attention,
                true
              )
              self.$Notice.success({
                title: "上传成功！",
              })
              window.location.reload()
            } else {
              self.$Notice.success({
                title: "上传失败",
              })
            }
          }).catch(function (error) {})
        } else {
          self.$Notice.success({
            title: "上传失败！",
          })
        }
      },
      handleSuccess2 (res, file) {
        if(file.response.code == 0) {
          var qs = require('qs')
          var self = this
          this.$http.post(self.PRIFIX + '/api/personal/user/update', qs.stringify({
            'avatar': file.response.data.src,
            'userId': self.userInfo.id
          })).then(function (response) {
            response = response.data
            if (response.code == 0) {
              saveToLocal(
                self.userInfo.id,
                self.formValidate.name,
                file.response.data.src,
                self.formValidate.QQ,
                self.formValidate.sex,
                self.formValidate.email,
                self.formValidate.city,
                self.formValidate.birthday,
                self.formValidate.signature,
                self.userInfo.backgroundImg,
                self.userInfo.phone,
                self.userInfo.fans,
                self.userInfo.attention,
                true
              )
              self.$Notice.success({
                title: "上传成功！",
              })
              window.location.reload()
            } else {
              self.$Notice.success({
                title: "上传失败",
              })
            }
          }).catch(function (error) {})
        } else {
          self.$Notice.success({
            title: "上传失败！",
          })
        }
      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: '上传失败,格式错误！'
        })
      },
      handleMaxSize (file) {
        this.$Notice.warning({
          title: '上传失败,大小不能超过2M'
        });
      },
      reset() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/user/checkCode', qs.stringify({
          'code': this.formValidate.validationCode
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
          } else {
            self.$Notice.error({
              title: response.message
            })
          }
        }).catch(function (error) {
        })
      },
    },
    created() {
      let phe = readLocal()
      this.formValidatePhone.phone = phe.phone
    }

  }

</script>

<style lang="less" scoped>

