    display: flex;
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .left {
    flex: 0 0 710px;
    width: 710px;
    margin-right: 20px;
  }

  .right {
    flex: 1;
    width: 340px;
  }

  .top_wrapper {
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .person-top-wrapper {
    width: 1036px;
    height: 160px;
  }

  .person-top-wrapper:hover .person-top-background {
    opacity: 1;
  }

  .person-top-background {
    opacity: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.4);
    color: #ffffff;
    width: 1036px;
    height: 160px;
    font-size: 18px;
    border-radius: 5px;
    transition: all 0.4s;
  }

  .upload-background {
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 5px;
    border-radius: 10px;
    text-align: center;
    line-height: 27px;
  }

  .upload-background:hover {
    cursor: pointer;
  }

  .person-top {
    background-repeat: no-repeat;
    background-size: 100%;
    width: 1036px;
    height: 160px;
    border-radius: 5px;
  }

  .person-bottom {
  }

  .avatar {
    display: block;
    width: 120px;
    height: 70px;
    margin: 0 auto;
  }

  .avatar:hover .avatar-background {
    opacity: 1;
  }

  .avatar img {
    position: absolute;
    width: 120px;
    height: 120px;
    border-radius: 10px;
    margin-top: -50px;
    z-index: 10;
  }

  .avatar-background {
    position: absolute;
    z-index: 11;
    font-size: 16px;
    text-align: center;
    line-height: 120px;
    width: 120px;
    height: 120px;
    background-color: rgba(0, 0, 0, 0.4);
    color: #ffffff;
    margin-top: -50px;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.4s;
    opacity: 0;
  }

  .big-name {
    margin-top: 10px;
    text-align: center;
  }

  .person-content {
    margin-top: 20px;
  }

  .inputButton {
    margin-top: 10px;
  }

</style>



