<template>
  <div id="app">
    <router-view :userInfo="userInfo"></router-view>
  </div>
</template>
<script>
  import {saveToLocal, readLocal,saveNavCss} from "../src/common/js/store.js"

  export default {
    data() {
      return {
        /*用户信息*/
        userInfo: {},
        PRIFIX: "https://news.immnc.com",
      }
    },
    created() {
      this.userInfo = readLocal()
      saveNavCss(99)
      let f = readLocal()
      if (f) {
        var qs = require('qs')
        var self = this
        /*验证用户是否失效*/
        this.$http.post(this.PRIFIX + '/api/user/valid').then(function (response) {
          response = response.data
          if (response.code == 0) {
            let user = response.data.userVO
            saveToLocal(
              user.userId,
              user.nikeName,
              user.avatar,
              user.qq,
              user.sex,
              user.email,
              user.city,
              user.birthday,
              user.signature,
              user.backgroundImg,
              user.phone,
              user.fansCounts,
              user.followCounts,
              true
            )
          } else {
            saveToLocal(
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
              false
            )
          }
        }).catch(function (error) {})
      } else {
        saveToLocal(
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          false
        )
      }
    }
  }
</script>



