export function exceed1(text) {
  if(text.length > 45) {
    var newText = text.substring(0,45) + "...";
    return newText;
  }else {
    return text;
  }
}

export function exceed2(text) {
  if(text.length > 29) {
    var newText = text.substring(0,29) + "...";
    return newText;
  }else {
    return text;
  }
}




