<template>
  <div class="content">
    <div class="left">
      <div ref="el">
      <!--轮播图-->
      <Card>
        <div class="slideShow">
          <Carousel dots="none" :autoplay="false" @on-change="showContent">
            <CarouselItem v-for="(item,index) in articleDetails.img" :key="item.id" style="width: 682px;height: 497px;left: 0px;display: flex;align-items: center;justify-content: center;">
              <img class="img-hide" :src="dynamicUrl+item" :id="index">
              <img v-if="imgsCss == 0" class="imgW" :src="dynamicUrl+item">
              <img v-else class="imgH" :src="dynamicUrl+item">
            </CarouselItem>
          </Carousel>
        </div>
        <div class="title">
          <h2>{{articleDetails.title}}</h2>
        </div>
        <div class="value" v-for="(item,index) in articleDetails.imgDesc"
             v-show="index===showImgIndex()">
          <p v-if="articleDetails.original==0">{{item}} --此图片来自于网络,如有侵权请及时联系我们!</p>
          <div class="num">
            <span class="red">{{index+1}}</span>
            /
            <span>{{articleDetails.imgNum}}</span>
          </div>
        </div>
        <div class="label"></div>
      </Card>
      <div style="margin-top: 10px">
        <AritcleComment :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :comment="comment" @commentShare="commentShare"
                        :userInfo="userInfo" @replyContent="replyContent" @addGoodA="addGoodA"
                        @addGoodB="addGoodB" :articleId="articleId" :commentLoadNum="commentLoadNum"
                        @hideComment="hideComment" @showMoreComment="showMoreComment" @showMoreReply="showMoreReply"
                        @selectReplyContent="selectReplyContent" @sendMyComment="sendMyComment"
                        @commentShare2="commentShare2" :rmo="rmo"></AritcleComment>
      </div>
      <div class="titleC">
        相关推荐
      </div>
      <BodyContent :userInfo="userInfo" :article="articleRecommendAgen" :PRIFIX="PRIFIX"
                   :contentFlag="false" :dynamicUrl="dynamicUrl"></BodyContent>
      <div v-if="loadCssFlag" class="demo-spin-col">
        <Spin fix>
          <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </div>
      <div v-if="contentFlag">
        <div class="titleC">
          更多精彩
        </div>
        <div>
          <BodyContent style="width:100%" :userInfo="userInfo" :article="articleRecommendMore" :PRIFIX="PRIFIX"
                       :contentFlag="false" :dynamicUrl="dynamicUrl"></BodyContent>
          <div v-if="loadCssFlag" class="demo-spin-col">
            <Spin fix>
              <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
              <div>Loading</div>
            </Spin>
          </div>
        </div>
      </div>
      </div>
    </div>
    <div class="right">
      <div ref="elr">
        <AuthorRecommendImgs :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :author="author" @attentionAuthorYES="attentionAuthorYES">
        </AuthorRecommendImgs>
        <Share :collection="articleDetails.collection" @collectArticleYES="collectArticleYES" :PRIFIX="PRIFIX"
               @collectArticleNO="collectArticleNO" @share="share"></Share>
        <Model1 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :realTimeNews="recommendList.realTimeNews" style="margin-top: 10px"></Model1>
        <Model3 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :imgs="recommendList.recommendImgs" style="margin-top: 10px"></Model3>
        <Model4 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :videos="recommendList.recommendVideos" style="margin-top: 10px"></Model4>
      </div>

      <Affix v-if="mo" :offset-top="65">
        <model1 :PRIFIX="PRIFIX" :realTimeNews="recommendList.realTimeNews" :dynamicUrl="dynamicUrl"></model1>
      </Affix>
    </div>

    <UserLogin :userInfo="userInfo" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></UserLogin>
    <BackTop></BackTop>

  </div>
</template>

<script type="text/ecmascript-6">
  import AuthorRecommendImgs from "../components/authorRecommend/authorRecommendImgs.vue"
  import Share from "../../common/components/share/share.vue"
  import AritcleComment from "../../common/components/comment/articleComment.vue"
  import Model1 from "../../index/components/recommendModel/model1/model1.vue"
  import Model3 from "../../index/components/recommendModel/model3/model3.vue"
  import Model4 from "../../index/components/recommendModel/model4/model4.vue"
  import BodyContent from "../../index/components/body/body.vue"
  import UserLogin from "../../common/components/userLogin/userLogin.vue"
  import {saveURL} from "../../../../src/common/js/store.js"

  export default{
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {
            name: 'keyWords',
            content: this.metaContent
          },
          {
            name: 'description',
            content: this.metaDescription
          }
        ]
      }
    },
    props: {
      userInfo: Object,
      recommendList: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    data() {
      return {
        metaDescription: '原创文章',
        metaContent: '妙漫网创',
        title: '妙漫网创',
        contentFlag: false,
        moreFlag: false,
        articleId: -1,
        imgIndex: 0,
        lastIndex: 0,
        articleDetails: {},
        comment: {},
        author: {},
        /*推荐文章 这里不要显示轮播图的文章！！！*/
        articleRecommendAgen: [],
        articleRecommendMore: [],
        /*模拟加载*/
        article: {},
        mo: false,
        commentLoadNum: false,
        commentI: 2,
        replyI: 2,
        rmo: false,
        loadCssFlag: false,
        imgsCss: 0
      }
    },
    components: {
      AuthorRecommendImgs,
      Share,
      AritcleComment,
      Model1,
      Model3,
      Model4,
      UserLogin,
      BodyContent
    },
    methods: {
      /*切换轮播图*/
      showContent(oldValue, value) {
        let w = window.document.getElementById(value).clientWidth
        let h = window.document.getElementById(value).clientHeight
        if (w > h) {
          this.imgsCss = 0
        } else {
          this.imgsCss = 1
        }
        this.imgIndex = value;
        this.lastIndex

      },
      showImgIndex() {
        return this.imgIndex
      },
      /*举报*/
      share(type, val) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/report/save', qs.stringify({
          'aid': self.articleDetails.id,
          'typeId': type,
          'info': val
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.$Notice.success({
              title: '举报成功！',
            })
          } else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: '举报失败！',
            })
          }
        }).catch(function (error) {})
      },
      commentShare(type, val1, val2) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/reply/report', qs.stringify({
          'content': val1,//举报发送的内容
          'typeId': type,//举报的类型
          'commentId': val2.byReplyId,//举报评论的Id
          'disposeState': 0//举报状态 就是0
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.$Notice.success({
              title: '举报成功！',
            })
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '举报失败！',
            })
          }
        }).catch(function (error) {
        })
      },
      commentShare2(type, val1, val2) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/comment/report', qs.stringify({
          'content': val1,//举报发送的内容
          'typeId': type,//举报的类型
          'commentId': val2.byReplyId,//举报评论的Id
          'disposeState': 0//举报状态 就是0
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.$Notice.success({
              title: '举报成功！',
            })
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '举报失败！',
            })
          }
        }).catch(function (error) {
        })
      },
      /*查看跟多评论*/
      showMoreComment() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/details', qs.stringify({
          'page': self.commentI,
          'size': 12,
          'articleId': self.articleId,
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            if(response.data.comment.pageContent.length == 0) {
              self.hideComment()
            }else {
              response.data.comment.pageContent.forEach((item) => {
                self.comment.pageContent.push(item)
              })
              self.comment.pageContent.forEach((item) => {
                self.$set(item, 'reply', {})
              })
              self.commentI++
            }
          } else {
            self.$Notice.error({
              title: response.message,
            })
          }
        }).catch(function (error) {})
      },
      /*查看回复*/
      selectReplyContent(index, id) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/reply/list', qs.stringify({
          'page': 1,
          'size': 12,
          'commentId': id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            if(response.data.pageContent.totalPages > 1) {
              self.rmo = true
            }
            self.comment.pageContent[index].reply = response.data.pageContent
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: "查询评论失败！",
            })
          }
        }).catch(function (error) {
        })
      },
      /*查看跟多回复*/
      showMoreReply(id) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/reply/list', qs.stringify({
          'page': self.replyI,
          'size': 12,
          'commentId': id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            if(response.data.pageContent == 0) {
              self.rmo = false
            } else {
              self.rmo = true
              response.data.pageContent.forEach((item) => {
                self.$set(item, 'reply', {})
                self.reply.pageContent.push(item)
              })
              self.replyI++
            }
          } else {
            self.$Notice.error({
              title: response.message,
            })
          }
        }).catch(function (error) {})
      },
      /*隐藏查看更多评论显示条*/
      hideComment() {
        this.commentLoadNum = false
      },
      /*评论*/
      sendMyComment(val) {
        let myComment = {
          id: this.userInfo.id + Math.round(Math.random() * 10),
          userID: this.userInfo.id,
          username: this.userInfo.name,
          avatar: this.userInfo.avatar,
          time: "刚刚",
          commentInfo: val,
          /*是否点过赞 0表示没点 1表示点了*/
          goodFlag: 0,
          good: 0,
          /*回复数量*/
          replyNum: 0,
          /*回复*/
          reply: {},
        }

        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/comment/save', qs.stringify({
          'aid': self.articleId,
          'commentInfo': val//评论的内容
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.comment.pageContent.unshift(myComment)
            self.comment.commentNum += 1
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: "评论失败！",
            })
          }
        }).catch(function (error) {})

      },
      /*回复评论*/
      replyContent(val) {
        var replyContentInfo = {
          avatar: this.userInfo.avatar,
          byReplyInfo: "",
          good: 0,
          goodFlag: 0,
          id: 1,
          replyInfo: val.replyInfo,
          time: "刚刚",
          username: this.userInfo.name,
          byUsername: val.byUsername,
          byReplyId: "",
          userID: val.userID
        }
        if(val.BBID !== val.id) {
          replyContentInfo.byReplyInfo = val.byReplyInfo
        }
        if (val.index != -1) {
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + '/api/article/reply/save', qs.stringify({
            'commId': val.id,//评论的Id
            'cid': val.rid,//被回复者的id
            'replyInfo': val.replyInfo,//回复的内容
            'byReplyId': val.byReplyId//被回复内容的id
          })).then(function (response) {
            response = response.data
            replyContentInfo.byReplyId = response.data.reply.byReplyId
            if (response.code == 0) {
              replyContentInfo.id = response.data.reply.id
              self.comment.pageContent[val.index].reply.pageContent.unshift(replyContentInfo)
            }else if(response.code === 3) {
              self.$Notice.error({
                title: '请登录！',
              })
              let url = window.location.href + ''
              saveURL(url)
              setTimeout(function () {
                self.$router.push('/login')
              },500)
            } else {
              self.$Notice.error({
                title: "回复失败！",
              })
            }
          }).catch(function (error) {
          })


        }

      },
      /*收藏*/
      collectArticleYES() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/collect', qs.stringify({
          'aid': self.articleDetails.id,//文章id
          'typeId': self.articleDetails.type//文章类型
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.articleDetails.collection = 1
            self.articleDetails.collection = response.data.collectId
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: "收藏失败！",
            })
          }
        }).catch(function (error) {
        })

      },
      collectArticleNO() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/collect/delete', qs.stringify({
          'collectId': self.articleDetails.collection//收藏id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.articleDetails.collection = 0
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: "取消收藏失败！",
            })
          }
        }).catch(function (error) {
        })
      },
      /*作者关注*/
      attentionAuthorYES() {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/user/attention', qs.stringify({
          'fid': self.author.userId, //被关注的人的id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.author.attention = 1
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: "关注失败！",
            })
          }
        }).catch(function (error) {
        })
      },
      /*点赞*/
      addGoodA(index, id) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/comment/praise', qs.stringify({
          'commentId': id//评论id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.comment.pageContent[index].good += 1
            self.comment.pageContent[index].goodFlag = 1
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: "点赞失败！",
            })
          }
        }).catch(function (error) {
        })
      },
      addGoodB(index1, index2, id) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/reply/praise', qs.stringify({
          'replyId': id//评论id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.comment.pageContent[index1].reply.pageContent[index2].good += 1
            self.comment.pageContent[index1].reply.pageContent[index2].goodFlag = 1
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: "点赞失败！",
            })
          }
        }).catch(function (error) {
        })
      },
      /*推送文章*/
      initArticle(keyword) {
        var ii = 1
        var qs = require('qs')
        var self = this
        self.loadCssFlag = true
        var ss = ''
        for (let a=0;a<keyword.length;a++) {
          if(a == keyword.length-1){
            ss += keyword[a]
          } else {
            ss = keyword[a] + ','
          }
        }
        this.$http.post(this.PRIFIX + '/api/homePage/recommend/article', qs.stringify({
          'page': ii,
          'size': 10,
          'content': ss
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            if (response.data.article.totalPages == response.data.article.currentPage) {
              response.data.article.pageContent.forEach((item) => {
                self.articleRecommendAgen.push(item)
              })
              self.contentFlag = true
              self.loadCssFlag = false
              self.moreFlag = true
            } else {
              response.data.article.pageContent.forEach((item) => {
                self.articleRecommendAgen.push(item)
              })
              self.loadCssFlag = false
            }
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
            self.contentFlag = true
            self.loadCssFlag = false
          }
        }).catch(function (error) {
          self.contentFlag = true
          self.loadCssFlag = false
        })
      }
    },
    mounted () {
      var i = 2
      var ii = 2
      this.$nextTick(() => {
        window.addEventListener('scroll', () => {
        /*内容高度*/
        let elHeight = this.$refs.el.clientHeight + 80
        /*获取左边内容高度*/
        let elrHeight = this.$refs.elr.clientHeight + 80 + window.innerHeight
        /*滚动高度*/
        let height = 0
        if (document.body.scrollTop) {
        height = document.body.scrollTop
      }
      if (document.documentElement.scrollTop) {
        height = document.documentElement.scrollTop
      }
      /*右边滚动显示推荐*/
      if(height + window.innerHeight >= elrHeight) {
        this.mo = true
      } else {
        this.mo = false
      }
      if (height + window.innerHeight >= elHeight) {
        if(!this.contentFlag) {
          /*推送文章*/
          var qs = require('qs')
          var self = this
          self.loadCssFlag = true
          var s = ''
          for (let a=0;a<self.articleDetails.keyword.length;a++) {
            if(a == self.articleDetails.keyword.length-1){
              s += self.articleDetails.keyword[a]
            } else {
              s = self.articleDetails.keyword[a] + ','
            }
          }
          this.$http.post(this.PRIFIX + '/api/homePage/recommend/article', qs.stringify({
            'page': i,
            'size': 10,
            'content': s
          })).then(function (response) {
            response = response.data
            if (response.code === 0) {
              if (response.data.article.totalPages == response.data.article.currentPage) {
                response.data.article.pageContent.forEach((item) => {
                  self.articleRecommendAgen.push(item)
              })
                self.contentFlag = true
                self.loadCssFlag = false
                self.moreFlag = true
              } else {
                response.data.article.pageContent.forEach((item) => {
                  self.articleRecommendAgen.push(item)
              })
                self.loadCssFlag = false
              }
            } else {
              self.$Notice.error({
                title: '内容获取失败',
              })
              self.contentFlag = true
              self.loadCssFlag = false
            }
          }).catch(function (error) {
            self.contentFlag = true
            self.loadCssFlag = false
          })
          i++
        }
        if(this.moreFlag) {
          /*更多文章*/
          var qs = require('qs')
          var self = this
          self.loadCssFlag = true
          this.$http.post(this.PRIFIX + '/api/homePage/index/article', qs.stringify({
            'page': ii,
            'size': 10,
            'content': s
          })).then(function (response) {
            response = response.data
            if (response.code === 0) {
              if (response.data.article.totalPages == response.data.article.currentPage) {
                response.data.article.pageContent.forEach((item) => {
                  self.articleRecommendMore.push(item)
                })
                self.loadCssFlag = false
                self.moreFlag = false
              } else {
                response.data.article.pageContent.forEach((item) => {
                  self.articleRecommendMore.push(item)
                })
                self.loadCssFlag = false
              }
            } else {
              self.$Notice.error({
                title: '内容获取失败',
              })
              self.loadCssFlag = false
              self.moreFlag = false
            }
          }).catch(function (error) {
            self.loadCssFlag = false
            self.moreFlag = false
          })
          ii++
        }
      }
    })
    })
    },
    created() {
      this.articleId = this.$route.query.articleId
      if(this.articleId == "" || this.articleId == null) {
      } else {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/details', qs.stringify({
          'page': 1,
          'size': 10,
          'articleId': self.articleId,
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.title = response.data.articleDetails.title
            self.metaDescription = response.data.articleDetails.title
            let key =  response.data.articleDetails.keyword
            var ss = ''
            for (let a=0;a<key.length;a++) {
              if(a == key.length-1){
                ss += key[a]
              } else {
                ss = key[a] + ','
              }
            }
            self.metaContent = ss

            self.articleDetails = response.data.articleDetails
            self.author = response.data.author
            self.comment = response.data.comment
            if(response.data.comment.commentNum > 12) {
              self.commentLoadNum = true
            }
            self.comment.pageContent.forEach((item) => {
              self.$set(item, 'reply', {})
          })
            self.initArticle(response.data.articleDetails.keyword)
          } else {
            self.$Notice.error({
              title: response.message,
            })
          }
        }).catch(function (error) {})
      }
      this.$nextTick(() => {
        var qs = require('qs')
        var self = this
        setTimeout(function() {
          let w = window.document.getElementById('0').clientWidth
          let h = window.document.getElementById('0').clientHeight
          if (w > h) {
            self.imgsCss = 0
          } else {
            self.imgsCss = 1
          }
        },500)
      })
    }
  }

</script>

<style lang="less" scoped>

  .content {
    display: flex;
    position: relative;
    top: 70px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .slideShow {
    min-height: 497px;
    max-width: 700px;
    overflow: hidden;
    background-color: #000;
    text-align: center;
  }

  .left {
    flex: 0 0 700px;
    width: 700px;
    margin-right: 20px;
  }

  .title h2 {
    margin-top: 5px;
    color: #222222;
  }

  .imgW {
    background-repeat: no-repeat;
    width: 100%;
  }

  .imgH {
    background-repeat: no-repeat;
    height: 100%;
  }

  .num {
    display: inline-block;
    float: right;
    font-size: 14px;
  }

  .red {
    color: #ed3f14;
  }

  .value {
    margin-bottom: 15px;
  }

  .img-hide {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

</style>



