<template>
  <div ref="el" class="content">
    <div class="left">
      <!--动态-->
      <div style="margin-bottom: 10px" v-for="(item,index) in dynamicContent.dynamic.pageContent">
        <personDynamic :dynamic="item" :index="index" :PRIFIX="PRIFIX" @addGood="addGood"
        @share="share"></personDynamic>
      </div>
    </div>
    <div class="right">
      <!--登录之后-->
      <PersonIntro v-if="userInfoFlag || userInfoFlag==='true'" :userInfo="userInfo" :PRIFIX="PRIFIX">
      </PersonIntro>
      <!--登录-->
      <Login v-if="!userInfoFlag || userInfoFlag==='false'"></Login>
      <!--推荐模块-->
      <RecommendModel :recommend="recommendList" :PRIFIX="PRIFIX"></RecommendModel>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import PersonIntro from "../index/components/personIntro/personIntro.vue"
  import RecommendModel from "../index/components/recommendModel/recommendModel.vue"
  import Login from "../index/components/personIntro/login.vue"
  import personDynamic from "./personDynamic/personDynamic.vue"
  import Model1 from "../index/components/recommendModel/model1/model1.vue"
  import Model3 from "../index/components/recommendModel/model3/model3.vue"
  import Model4 from "../index/components/recommendModel/model4/model4.vue"
  import {loadFromLocal} from "../../../src/common/js/store.js"

  export default {
    props: {
      userInfo: Object,
      recommendList: Object,
      PRIFIX: String
    },
    data() {
      return {
        flag: false,
        /*看他说*/
        dynamicContent: {}
      }
    },
    methods: {
      addGood(index,id) {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/dynamic/praise', qs.stringify({
          'userId': self.userInfo.id,
          'dynamicId': id
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.dynamicContent.dynamic.pageContent[index].good += 1
            self.dynamicContent.dynamic.pageContent[index].goodFlag = 1
          } else {
            self.$Notice.error({
              title: "点赞失败！",
            })
          }
        }).catch(function (error) {})
      },
      share(id,type,val) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/article/report/save', qs.stringify({
          'uid': self.userInfo.id,
          'aid': id,
          'typeId': type,
          'info': val
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.$Notice.success({
              title: '举报成功！',
            })
          } else {
            self.$Notice.error({
              title: '举报失败！',
            })
          }
        }).catch(function (error) {
        })
      },
    },
    components: {
      PersonIntro,
      RecommendModel,
      Login,
      personDynamic,
      Model1,
      Model3,
      Model4
    },
    mounted () {
      var i = 2
      this.$nextTick(() => {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/dynamic/list',qs.stringify({
          'page':i,
          'size':6,
          'userId': self.userInfo.id
          })).then(function (response) {
          response = response.data
          if (response.code === 0) {
          self.dynamicContent = response.data
          } else {
          self.$Notice.error({
            title: '内容获取失败',
          })
          }
        }).catch(function (error) {})
      })
      i++
    },
    created() {
      this.userInfoFlag = loadFromLocal()
      this.$nextTick(() => {
        if (this.userInfo == "" || this.userInfo == null) {
          this.flag = false
        } else {
          this.flag = true
        }

        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/dynamic/list',qs.stringify({
          'page':1,
          'size':6,
          'userId': self.userInfo.id
        })).then(function (response) {
          response = response.data
          console.log(response)
          if (response.code === 0) {
            self.dynamicContent = response.data
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})
      })
    },
  }
</script>

<style lang="less" scoped>
  .content {
    display: flex;
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
  }

  .left {
    flex: 0 0 730px;
    width: 730px;
    height: 100%;
    margin-right: 20px;
  }

  .right {
    flex: 1;
    width: 340px;
    height: 100%;
    position: relative;
  }

  .card {
    position: relative;
    clear: both;
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 10px #999;
    -moz-box-shadow: 0 0 10px #999;
    box-shadow: 0 0 10px #999;
  }

  .go {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 25px #999;
    -moz-box-shadow: 0 0 25px #999;
    box-shadow: 0 0 25px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveGo 0.5s;
  }

  .to {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 10px #999;
    -moz-box-shadow: 0 0 10px #999;
    box-shadow: 0 0 10px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveTo 0.5s;
  }

  @keyframes moveGo {
    from {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
    to {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
  }

  @-webkit-keyframes moveGo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
    to {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
  }

  @keyframes moveTo {
    from {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
    to {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
  }

  @-webkit-keyframes moveTo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 25px #999;
      -moz-box-shadow: 0 0 25px #999;
      box-shadow: 0 0 25px #999;
    }
    to {
      -webkit-box-shadow: 0 0 10px #999;
      -moz-box-shadow: 0 0 10px #999;
      box-shadow: 0 0 10px #999;
    }
  }

</style>



