<template>
  <Card class="card2">
    <div class="card2Wrapper">
      <!--跳转到视频-->
      <router-link target="_blank" :to="{path:'/video',query:{articleId:content.id}}">
        <div class="card2-left">
          <div v-if="content.original==1" class="labelImg">
            <p>原创</p>
          </div>
          <div class="card2-img-background"></div>
          <div class="play-icon">
            <img src="../../../../../assets/img/icon/播放.svg">
          </div>
          <img class="card2-img" :src="dynamicUrl+content.img[0]">
        </div>
      </router-link>

      <div class="card2-right">
        <!--跳转到视频-->
        <router-link target="_blank" :to="{path:'/video',query:{articleId:content.id}}">
          <p style="width: 474px;" class="card2-title">{{content.title | exceedFont(content.title)}}</p>
        </router-link>
        <div class="card2-description">
          <div class="card2-description-wrapper">
            <!--跳转到分类-->
            <Sort sort="视频" class="margin-right8px"></Sort>
            <Sort :sort="content.sort" class="margin-right8px"></Sort>
            <p class="margin-right8px width1"></p>
            <!--跳转到作者-->
            <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:content.author.userId}}">
              <Author :author="content.author" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"
                      class="margin-right8px"></Author>
            </router-link>
            <span class="margin-right8px">·</span>
            <!--跳转到视频-->
            <router-link target="_blank" :to="{path:'/video',query:{articleId:content.id}}">
              <Comment :commentNum="content.commentNum" class="margin-right8px"></Comment>
            </router-link>
            <span class="margin-right8px">·</span>
            <span class="margin-right8px time">{{content.nowTime}}浏览</span>
          </div>
          <div v-show="collect" class="del" @click="del">
            <a>
              <Icon type="ios-trash"></Icon>
              删除
            </a>
          </div>
        </div>
      </div>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">
  import Sort from "../../../../common/components/sort/sort.vue"
  import Author from "../../../../common/components/author/author.vue"
  import Comment from "../../../../common/components/comment/comment.vue"
  import {exceed1} from "../../../../../common/js/exceed"

  export default {
    props: {
      content: Object,
      collect: Boolean,
      PRIFIX: String,
      dynamicUrl: String
    },
    components: {
      Sort,
      Author,
      Comment
    },
    filters: {
      exceedFont(text) {
        return exceed1(text)
      }
    },
    methods: {
      del() {
        this.$emit("delArticle", this.content.collectId, this.content.type)
      }
    }
  }
</script>

<style lang="less" scoped>
  .card1 {
    margin: 6px 0px;
  }

  .card2 {
    margin: 6px 0px;
  }

  .width1 {
    display: inline-block;
    height: 10px;
    width: 5px;
  }

  .card2-description {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .margin-right8px {
    margin-right: 6px;
  }

  .title {
    font-size: 20px;
    color: #222222;
    margin-bottom: 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: 700;
  }

  .card2Wrapper {
    display: table;
  }

  .card2-title {
    display: inline-block;
    font-size: 20px;
    color: #222222;
    margin-bottom: 4px;
    overflow: hidden;
    font-weight: 700;
  }

  .title:hover, .card2-title:hover {
    border-radius: 5px;
    color: #406599;
    cursor: pointer;
  }

  .card2-img {
    max-width: 156px;
    min-height: 110px;
    border-radius: 5px;
    display: inline-block;
    transition: all 0.6s;
    z-index: 7;
  }

  .card2-left {
    display: inline-block;
    margin-right: 20px;
    width: 156px;
    height: 110px;
    overflow: hidden;
    border-radius: 10px;
  }

  .card2-left:hover .card2-img-background {
    border-radius: 5px;
    cursor: pointer;
    opacity: 1;
  }

  .card2-left:hover .card2-img {
    cursor: pointer;
    transform: scale(1.1);
  }

  .card2-left:hover .play-icon img {
    cursor: pointer;
  }

  .card2-right {
    width: 100%;
    padding-right: 10px;
    height: 110px;
    display: table-cell;
    vertical-align: middle;
  }

  .card2-description-wrapper {
    display: flex;
    align-items: center;
  }

  .del {
    display: inline-block;
    float: right;
  }

  .del a {
    color: #777;
  }

  .del a:hover {
    color: #ed4040;
  }

  .card2-img-background {
    position: absolute;
    width: 156px;
    height: 110px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 0;
    z-index: 8;
    transition: all 0.6s;
  }

  .play-icon img {
    position: absolute;
    margin-left: 58px;
    margin-top: 35px;
    z-index: 9;
  }

  .labelImg {
    position: absolute;
    width: 156px;
    height: 110px;
    overflow: hidden;
    transition: all 0.6s;
    z-index: 11;
  }

  .labelImg:hover {
    cursor: pointer;
    transform: scale(1.0);
  }

  .labelImg:hover + .card2-img {
    transform: scale(1.1);
  }

  .labelImg p {
    text-align: center;
    color: #ffffff;
    background-color: #ed4040;
    padding: 1px 35px;
    margin-left: -33px;
    display: inline-block;
    font-size: 14px;
    position: absolute;
    transform: rotate(-30deg);
    -ms-transform: rotate(-30deg); /* IE 9 */
    -webkit-transform: rotate(-30deg); /* Safari and Chrome */
  }

</style>



