<template>
  <div ref="sort" class="sort">{{sort}}</div>
</template>

<script type="text/ecmascript-6">
  export default{
    props: {
      sort: {
        type: String
      }
    },
    data() {
      return {
        color: ""
      }
    },
    mounted: function () {
      this.$nextTick(function () {
        switch (this.sort) {
          case "图文":
            this.color = "#2d8cf0"
            break;
          case "多图":
            this.color = "#ff9900"
            break;
          case "视频":
            this.color = "#19be6b"
            break;
          default:
            this.color = "#495060"
        }
        this.$refs.sort.style.borderColor = this.color
        this.$refs.sort.style.color = this.color
      })
    }
  }
</script>

<style lang="less" scoped>

  .sort {
    display: inline-block;
    padding: 1px 2px;
    border-radius: 4px;
    border: 1px solid;
    font-size: 12px;
    vertical-align: middle;
  }
</style>



