            <TabPane label="视频" name="name4">
              <div style="margin-top: 30px" v-if="userContent.video.pageContent.length == 0">
                <div class="notContent">~~~客官,这里空空如也哦~~~</div>
              </div>
              <div v-else v-for="(item,index) in userContent.video.pageContent" class="margin-bottom-10px">
                <Bmodel3 :dynamicUrl="dynamicUrl" @delArticle="delArticle" :PRIFIX="PRIFIX" :content="item" :collect="true"></Bmodel3>
              </div>
              <div v-if="loadCssFlag" class="demo-spin-col">
                <Spin fix>
                  <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                  <div>Loading</div>
                </Spin>
              </div>
            </TabPane>
            <TabPane label="关注" name="name5">
              <div style="margin-top: 30px" v-if="userContent.attention.pageContent.length == 0">
                <div class="notContent">~~~客官,这里空空如也哦~~~</div>
              </div>
              <div v-else v-for="(item,index) in userContent.attention.pageContent" class="margin-bottom-10px">
