              <div v-if="loadCssFlag" class="demo-spin-col">
                <Spin fix>
                  <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                  <div>Loading</div>
                </Spin>
              </div>
            </TabPane>
            <!--<TabPane label="通知" name="name7">
              <div v-for="(item,index) in userContent.notice.pageContent" class="margin-bottom-10px">
                <Message :notice="item" :index="index" @delMessage="delMessage"></Message>
              </div>
            </TabPane>-->
          </Tabs>
        </div>
      </div>

      <div class="right">
        <Attention :attentionNum="userContent.attentionNum"
                   :fansNum="userContent.fansNum"></Attention>
        <div class="margin-top-10px">
