    <Modal v-model="modal9" title="作者申请">
      <Steps :current="current" style="display: flex">
        <Step title="申请作者" style="display: flex"></Step>
        <Step title="等待审核" style="display: flex"></Step>
        <Step title="结果" style="display: flex"></Step>
      </Steps>
      <div v-if="current == 0">
        <Input v-model="reason" style="width: 96%;margin: 20px 10px 10px 10px" type="textarea" :autosize="true" placeholder="申请原因"></Input>
        <Button style="margin: 10px" type="primary" @click="setAuthor">申请成为作者</Button>
      </div>
      <p v-if="current == 1" style="height: 20px;font-size: 20px;margin: 20px;text-align: center;">审核中...</p>
      <div v-if="current == 3">
        <p style="height: 20px;font-size: 20px;margin: 20px;color:#ed3f14;text-align: center;">审核失败</p>
        <Button type="primary" @click="again">重新申请</Button>
      </div>
      <p v-if="current == 2" style="height: 20px;font-size: 20px;margin: 20px;color:#19be6b;text-align: center;">审核成功</p>
