<script type="text/ecmascript-6">
  import Bmodel1 from "../index/components/body/model1/model1.vue"
  import Bmodel2 from "../index/components/body/model2/model2.vue"
  import Bmodel3 from "../index/components/body/model3/model3.vue"
  import Browse from "./components/browse.vue"
  import Attention from "./components/attention.vue"
  import Issue from "./components/issue.vue"
  import AttentionPerson from "../common/components/attention/attention.vue"
  import Message from "../common/components/message/message.vue"
  import PrivateLetter from "../common/components/privateLetter/privateLetter.vue"
  import PersonDynamic from "../dynamic/personDynamic/personDynamic.vue"
  import {saveToLocal,saveURL} from "../../../src/common/js/store.js"

  export default {
    props: {
      userInfo: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    data () {
      return {
        /*判断用户是前台还是后台*/
        userType: -1,
        /*判断用户步骤*/
        current: 0,
        reason: '',
        modal9: false,
        /*控制私信弹出框*/
        privateLetterModal: false,
        /*用户*/
        userContent:{},
        tabName: "name1",
        issueId: "",
        chatMessages: [],
        contentFlag: false,
        contentFlag1: false,
        contentFlag2: false,
        contentFlag3: false,
        contentFlag4: false,
        contentFlag5: false,
        contentFlag6: false,
        loadCssFlag: false
      }
    },
    components: {
      Bmodel1,
      Bmodel2,
      Bmodel3,
      Browse,
      Attention,
      Issue,
      AttentionPerson,
      Message,
      PrivateLetter,
      PersonDynamic
    },
    methods: {
      showPrivateLetterModal1(id,index) {
        this.userContent.attention.pageContent[index].unread = 0
        this.privateLetterModal = true
        this.issueId = id
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/communication/list',qs.stringify({
          'uid': id,
          'size': 6,
          'page': 1
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.chatMessages = response.data.chatMessages.pageContent
          } else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: '数据获取失败',
            })
          }
        }).catch(function (error) {
          alert(error)
        })
      },
      showPrivateLetterModal2(id,index) {
        this.userContent.fans.pageContent[index].unread = 0
        this.privateLetterModal = true
        this.issueId = id
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/communication/list',qs.stringify({
          'uid': id,
          'size': 6,
          'page': 1
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.chatMessages = response.data.chatMessages.pageContent
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: '数据获取失败',
            })
          }
        }).catch(function (error) {})
      },
      hidePrivateLetterModal() {
        this.privateLetterModal = false
      },
      /*添加关注*/
      attentionYES(data1,data2) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/user/attention',qs.stringify({
          'fid': data2.aid
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            response = response.data.attention
            self.userContent.fans.pageContent[data1].eachOtherAttention = 1
            self.userContent.fans.pageContent[data1].attention = response.attention
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '关注失败',
            })
          }
        }).catch(function (error) {})
      },
      /*取消关注*/
      attentionNO1(data1,attentionInfo) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/user/attention/delete',qs.stringify({
          'attentionId': attentionInfo.attention
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.userContent.attention.pageContent.splice(data1,1)
            self.userContent.attention.pageContent[data1-1].eachOtherAttention = 0
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '取消关注失败',
            })
          }
        }).catch(function (error) {})
      },
      attentionNO2(data1,attentionInfo) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/user/attention/delete',qs.stringify({
          'attentionId': attentionInfo.attention
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.userContent.fans.pageContent[data1].eachOtherAttention = 0
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '取消关注失败',
            })
          }
        }).catch(function (error) {})
      },
      delMessage(index) {
        this.userContent.notice.pageContent.splice(index,1)
      },
      handleSuccess1 (res, file) {
        if(file.response.code == 0) {
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + '/api/personal/user/update', qs.stringify({
            'backgroundImg': file.response.data.src,
          })).then(function (response) {
            response = response.data
            if (response.code == 0) {
              saveToLocal(
                self.userInfo.id,
                self.userInfo.name,
                self.userInfo.avatar,
                self.userInfo.qq,
                self.userInfo.sex,
                self.userInfo.email,
                self.userInfo.city,
                self.userInfo.birthday,
                self.userInfo.signature,
                self.userInfo.phone,
                file.response.data.src,
                self.userInfo.fans,
                self.userInfo.attention,
                true
              )
              self.$Notice.success({
                title: "上传成功！",
              })
              window.location.reload()
            } else {
              self.$Notice.success({
                title: "上传失败",
              })
            }
          }).catch(function (error) {})
        } else if(response.code === 3) {
          self.$Notice.error({
            title: '请登录！',
          })
          let url = window.location.href + ''
          saveURL(url)
          setTimeout(function () {
            self.$router.push('/login')
          },500)
        } else {
          self.$Notice.success({
            title: "上传失败！",
          })
        }
      },
      handleSuccess2 (res, file) {
        if(file.response.code == 0) {
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + '/api/personal/user/update', qs.stringify({
            'avatar': file.response.data.src,
          })).then(function (response) {
            response = response.data
            if (response.code == 0) {
              saveToLocal(
                self.userInfo.id,
                self.userInfo.name,
                file.response.data.src,
                self.userInfo.qq,
                self.userInfo.sex,
                self.userInfo.email,
                self.userInfo.city,
                self.userInfo.birthday,
                self.userInfo.signature,
                self.userInfo.phone,
                self.userInfo.backgroundImg,
                self.userInfo.fans,
                self.userInfo.attention,
                true
              )
              self.$Notice.success({
                title: "上传成功！",
              })
              window.location.reload()
            } else {
              self.$Notice.success({
                title: "上传失败",
              })
            }
          }).catch(function (error) {})
        }else if(response.code === 3) {
          self.$Notice.error({
            title: '请登录！',
          })
          let url = window.location.href + ''
          saveURL(url)
          setTimeout(function () {
            self.$router.push('/login')
          },500)
        }  else {
          self.$Notice.success({
            title: "上传失败！",
          })
        }
      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: '上传失败,格式错误！'
        })
      },
      handleMaxSize (file) {
        this.$Notice.warning({
          title: '上传失败,大小不能超过2M'
        });
      },
      delArticle(id,type) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/collect/delete',qs.stringify({
          'collectId': id
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            if(type == 1) {
              for(let i=0;i<self.userContent.article.pageContent.length;i++) {
                if(self.userContent.article.pageContent[i].collectId == id) {
                  self.userContent.article.pageContent.splice(i,1)
                }
              }
            }
            if(type == 2) {
              for(let i=0;i<self.userContent.imgs.pageContent.length;i++) {
                if(self.userContent.imgs.pageContent[i].collectId == id) {
                  self.userContent.imgs.pageContent.splice(i,1)
                }
              }
            }
            if(type == 3) {
              for(let i=0;i<self.userContent.video.pageContent.length;i++) {
                if(self.userContent.video.pageContent[i].collectId == id) {
                  self.userContent.video.pageContent.splice(i,1)
                }
              }
            }
          }else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          }  else {
            self.$Notice.error({
              title: '取消收藏失败',
            })
          }
        }).catch(function (error) {})
      },
      select(name) {
        this.tabName = name
        if (name == "name6") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/fans/list',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.fans = response.data.fans
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }
        if (name == "name5") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/attention/list',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.attention = response.data.attention
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }
        if (name == "name4") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6,
              'typeId': 3
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.video = response.data.pageContent
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }
        if (name == "name3") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6,
              'typeId': 2
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.imgs = response.data.pageContent
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }
        if (name == "name2") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6,
              'typeId': 1
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.imgText = response.data.pageContent
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }
        if (name == "name1") {
          this.$nextTick(() => {
            var qs = require('qs')
            var self = this
            this.$http.post(self.PRIFIX + '/api/personal/user/dynamic/list',qs.stringify({
              'userId': self.userInfo.id,
              'page': 1,
              'size': 6
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.userContent.dynamic = response.data.dynamic
              }else if(response.code === 3) {
                self.$Notice.error({
                  title: '请登录！',
                })
                let url = window.location.href + ''
                saveURL(url)
                setTimeout(function () {
                  self.$router.push('/login')
                },500)
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
          })
        }

      },
      addGood(index,id) {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/dynamic/praise',qs.stringify({
          'dynamicId': id
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.userContent.dynamic.pageContent[index].good += 1
            self.userContent.dynamic.pageContent[index].goodFlag = 1
          } else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
