      /*举报*/
      share(id,val,type) {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/personal/dynamic/report', qs.stringify({
          'dynamicId': id,
          'typeId': type,
          'content': val
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.$Notice.success({
              title: '举报成功！',
            })
