      /*申请作者*/
      setAuthor () {
        if (this.reason == '') {
          self.$Notice.success({
            title: '请说明理由！',
          })
        } else {
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + '/oa/author/apply', qs.stringify({
            'reason': self.reason,
          })).then(function (response) {
            response = response.data
            if (response.code === 0) {
              self.$Notice.success({
                title: '申请成功！',
              })
              self.current = 1
            } else if(response.code === 3) {
              self.$Notice.error({
                title: '请登录！',
              })
              let url = window.location.href + ''
              saveURL(url)
              setTimeout(function () {
                self.$router.push('/login')
              },500)
            } else {
              self.$Notice.error({
                title: '申请失败！',
              })
            }
          }).catch(function (error) {})
        }
      },
      /*进入后台*/
      goBackground () {
        window.location.href = 'http://news.immnc.com:8888'
      },
      /*重新申请*/
      again () {
        this.current = 0
        this.reason = ''
      }
