      this.$nextTick(() => {
        window.addEventListener('scroll', () => {
          /*内容高度*/
          if(this.$refs.el.clientHeight){
            var elHeight = this.$refs.el.clientHeight + 80
          }
          /*滚动高度*/
          let height = 0
          if (document.body.scrollTop) {
            height = document.body.scrollTop
          }
          if (document.documentElement.scrollTop) {
            height = document.documentElement.scrollTop
          }
          if (height + window.innerHeight >= elHeight) {
              var qs = require('qs')
              var self = this
              if(this.tabName === "name1") {
                if(!this.contentFlag1) {
                  self.loadCssFlag = true
                  this.$http.post(self.PRIFIX + '/api/personal/user/dynamic/list',qs.stringify({
                    'page': a,
                    'size': 6,
                    'userId': self.userInfo.id
                  })).then(function (response) {
                    response = response.data
                    if(response.code == 0) {
                      if(response.data.dynamic.pageContent.length == 0){
                        self.contentFlag1 = true
                        self.loadCssFlag = false
                      } else {
                        let dynamic = response.data.dynamic.pageContent
                        dynamic.forEach((item) => {
                          self.userContent.dynamic.pageContent.push(item)
                        })
                        self.loadCssFlag = false
                      }
                    }
                    else {
