<template>
  <div ref="elIndex" class="content">
    <div class="left">
      <div class="card" onMouseOut="this.className='to'" onMouseOver="this.className='go'">
        <!--轮播图-->
        <SlideShow :slide="slide" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></SlideShow>
      </div>
      <div v-if="loadFlag" class="demo-spin-col">
        <Spin fix>
          <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </div>
      <transition name="hide">
        <Affix v-if="newFlag" :offset-top="60">
          <div class="demo-affix">
            <a style="color: #ffffff" @click="showNewArticle">{{newContent}}</a>
            <a style="color: #ffffff"><Icon class="close" type="close" @click="close"></Icon></a>
          </div>
        </Affix>
        <div v-if="newLoad" class="demo-spin-col">
          <Spin fix>
            <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          </Spin>
        </div>
      </transition>
      <!--内容-->
      <BodyContentIndex v-if="!loadFlag" :userInfo="userInfo" :article="articleRecommendAgen" :PRIFIX="PRIFIX"
                   :contentFlag="contentFlag" @showNewArticle="showNewArticle" :dynamicUrl="dynamicUrl"></BodyContentIndex>
      <div v-if="loadCssFlag" class="demo-spin-col">
        <Spin fix>
          <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </div>
    </div>
    <div class="right">
      <div ref="elr">
        <!--登录之后-->
        <PersonIntro v-if="userInfo.flag" :userInfo="userInfo" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl">
        </PersonIntro>
        <!--登录-->
        <Login v-else></Login>
        <!--推荐模块-->
        <RecommendModel :recommend="recommendList" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></RecommendModel>
      </div>

      <Affix v-if="mo" :offset-top="65">
        <model1 :PRIFIX="PRIFIX" :realTimeNews="recommendList.realTimeNews" :dynamicUrl="dynamicUrl"></model1>
      </Affix>
    </div>
    <!--实时热闻-->
    <BackTop></BackTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import SlideShow from "./components/slideshow/slideshow.vue"
  import BodyContentIndex from "./components/body/bodyContentIndex.vue"
  import PersonIntro from "./components/personIntro/personIntro.vue"
  import RecommendModel from "./components/recommendModel/recommendModel.vue"
  import Login from "./components/personIntro/login.vue"
  import {readLocal} from "../../../src/common/js/store.js"
  import model1 from "./components/recommendModel/model1/model1.vue"
  import {addTime} from "../../../src/common/js/time"

  export default {
    props: {
      recommendList: Object,
      slide: Array,
      PRIFIX: String,
      articleRecommendAgen: Array,
      dynamicUrl: String,
    },
    data() {
      return {
        userInfo: {},
        /*控制用户登录注册*/
        userInfoFlag: false,
        /*控制内容完了之后显示的看完了*/
        contentFlag: false,
        mo: false,
        nowTime: "",
        newFlag: false,
        time: 0,
        newContent: '有新的文章了哟~,点击查看',
        loadCssFlag: false,
        newLoad: false,
        loadFlag: true,
        i: 2,
      }
    },
    components: {
      SlideShow,
      BodyContentIndex,
      PersonIntro,
      RecommendModel,
      Login,
      model1
    },
    watch: {
      '$route' () {
        this.userInfo = readLocal()
      }
    },
    methods: {
      showNewArticle() {
        window.scrollTo(0,405)
        var qs = require('qs')
        var self = this
        self.newLoad = true
        self.newContent = '已为您推荐6篇新闻'
        setTimeout(function () {
          self.newFlag = false
        },3000)
        this.$http.post(this.PRIFIX + '/api/homePage/random/article',qs.stringify({
          'size': 6
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            response.data.article.pageContent.forEach((item) => {
              self.newLoad = false
              self.newFlag = true
              self.articleRecommendAgen.unshift(item)
            })
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
            self.newLoad = false
          }
        }).catch(function (error) {self.newLoad = false})
      },
      close() {
        this.newFlag = false
      },
      computedTime() {
        this.time = this.time + 1
        if(this.time == 300) {
          this.newFlag = true
        }
        return
      },
      hideNew() {
        this.newFlag = false
      },
      hideLoadCss() {
        this.loadFlag = false
        this.newFlag = true
      },
      handleScroll() {
        /*右边内容高度*/
        let elHeight = this.$refs.elIndex.clientHeight + 80
        /*获取左边内容高度*/
        let elrHeight = this.$refs.elr.clientHeight + 80 + window.innerHeight
        /*滚动高度*/
        let height = 0
        if (document.body.scrollTop) {
          height = document.body.scrollTop
        }
        if (document.documentElement.scrollTop) {
          height = document.documentElement.scrollTop
        }
        /*右边滚动显示推荐*/
        if(height + window.innerHeight >= elrHeight) {
          this.mo = true
        } else {
          this.mo = false
        }
        if (height + window.innerHeight >= elHeight) {
          if(!this.contentFlag) {
            /*推送文章*/
            var qs = require('qs')
            var self = this
            self.loadCssFlag = true
            this.$http.post(this.PRIFIX + '/api/homePage/index/article',qs.stringify({
              'page': self.i,
              'size': 6
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                if(response.data.article.pageContent.length == 0 ) {
                  self.contentFlag = true
                  self.loadCssFlag = false
                } else {
                  response.data.article.pageContent.forEach((item) =>{
                    self.articleRecommendAgen.push(item)
                  })
                  self.loadCssFlag = false
                }
              } else {
                self.$Notice.error({
                  title: '内容获取失败',
                })
              }
            }).catch(function (error) {})
            self.i++
          }
        }
      }
    },
    created () {
      this.userInfo = readLocal()
      this.newContent = '已为您推荐10篇新闻'
      setTimeout(this.hideLoadCss,1000)
      setTimeout(this.hideNew,3000)
      setInterval(this.computedTime,1000)
    },
    mounted () {
      this.$nextTick(() => {
        window.addEventListener('scroll', this.handleScroll)
      })
    },
    destroyed () {
      window.removeEventListener('scroll', this.handleScroll)
    },
  }
</script>

<style lang="less" scoped>
  .content {
    display: flex;
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
  }

  .left {
    flex: 0 0 730px;
    width: 730px;
    height: 100%;
  }

  .right {
    flex: 1;
    width: 340px;
    height: 100%;
    position: relative;
  }

  .card {
    position: relative;
    clear: both;
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 5px #999;
    -moz-box-shadow: 0 0 5px #999;
    box-shadow: 0 0 5px #999;
  }

  .go {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 15px #999;
    -moz-box-shadow: 0 0 15px #999;
    box-shadow: 0 0 15px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveGo 0.5s;
  }

  .to {
    width: 700px;
    height: 380px;
    border-radius: 8px;
    -webkit-box-shadow: 0 0 5px #999;
    -moz-box-shadow: 0 0 5px #999;
    box-shadow: 0 0 5px #999;
    animation: mymove 0.5s;
    -webkit-animation: moveTo 0.5s;
  }

  @keyframes moveGo {
    from {
      -webkit-box-shadow: 0 0 5px #999;
      -moz-box-shadow: 0 0 5px #999;
      box-shadow: 0 0 5px #999;
    }
    to {
      -webkit-box-shadow: 0 0 15px #999;
      -moz-box-shadow: 0 0 15px #999;
      box-shadow: 0 0 15px #999;
    }
  }

  @-webkit-keyframes moveGo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 5px #999;
      -moz-box-shadow: 0 0 5px #999;
      box-shadow: 0 0 5px #999;
    }
    to {
      -webkit-box-shadow: 0 0 15px #999;
      -moz-box-shadow: 0 0 15px #999;
      box-shadow: 0 0 15px #999;
    }
  }

  @keyframes moveTo {
    from {
      -webkit-box-shadow: 0 0 15px #999;
      -moz-box-shadow: 0 0 15px #999;
      box-shadow: 0 0 15px #999;
    }
    to {
      -webkit-box-shadow: 0 0 5px #999;
      -moz-box-shadow: 0 0 5px #999;
      box-shadow: 0 0 5px #999;
    }
  }

  @-webkit-keyframes moveTo /*Safari and Chrome*/
  {
    from {
      -webkit-box-shadow: 0 0 15px #999;
      -moz-box-shadow: 0 0 15px #999;
      box-shadow: 0 0 15px #999;
    }
    to {
      -webkit-box-shadow: 0 0 5px #999;
      -moz-box-shadow: 0 0 5px #999;
      box-shadow: 0 0 5px #999;
    }
  }

  .demo-affix{
    color: #fff;
    padding: 6px;
    text-align: center;
    background: rgba(96,163,245,.85);
    width: 700px;
    height: 32px;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-top: 5px;
    font-size: 14px;
  }

  .close{
    float: right;
    line-height: 20px;
    color: #fff;
    font-size: 12px;
  }

  .hide-enter-active, .hide-leave-active {
    height: 32px;
    transition: all 0.2s;
  }
  .hide-enter, .hide-leave-to /* .fade-leave-active below version 2.1.8 */ {
    height: 0px;
  }

</style>



