<template>
    <Card style="width: 340px" class="cc">
      <p class="description">登录后可以保存您的浏览喜好、评论、收藏，并与APP同步更可以发布微头条</p>
      <router-link to="/login">
        <Button class="login-button" type="error">登录</Button>
      </router-link>
      <div class="login">
        <div class="other">
          <div @click="saveUrlQ" class="qq">
            <img :src="qq">
            <p>QQ</p>
          </div>
          <div @click="saveUrlW" class="weixin">
            <img :src="wx">
            <p>微信</p>
          </div>
        </div>
      </div>
    </Card>
</template>

<script type="text/ecmascript-6">
  import {saveURL} from "../../../../../src/common/js/store.js"

  export default{
    data() {
      return{
        qq: "../../../../static/img/QQ.svg",
        wx: "./../../../static/img/微信.svg"
      }
    },
    methods: {
      saveUrlQ() {
        let url = window.location.href + ''
        saveURL(url)
        window.location.href = 'http://news.immnc.com:8888/oauth/qq/redirect'
      },
      saveUrlW() {
        let url = window.location.href + ''
        saveURL(url)
        window.location.href = 'http://news.immnc.com:8888/api/user/qrAuthorize?returnUrl=http://www.immnc.com/weixinLogin'
      }

    }
  }

</script>

<style lang="less" scoped>
  .login-button{
    width: 306px;
    margin: 0 auto;
    font-size: 14px;
    margin-top: 10px;
  }

  .description{
    text-align: center;
  }

  .other{
    margin: 0 auto;
    width: 88px;
    margin-top: 10px;
  }

  .qq{
    display: inline-block;
    margin-right: 28px;
  }

  .qq:hover{
    cursor: pointer;
  }

  .qq img{
    display: inline-block;
  }

  .weixin{
    display: inline-block;
  }

  .weixin:hover{
    cursor: pointer;
  }

  .weixin img{
    display: inline-block;
  }

</style>



