<template>
  <Card style="width: 340px;margin-top: 10px" class="cc">
    <div class="model3">
      <!--跳转到视频-->
      <router-link target="_blank" :to="{path:'sortPageType',query:{navType:3}}">
        <h4 style="color: #000000" class="hi">
          精彩视频
        </h4>
      </router-link>
      <ul class="img">

        <li v-for="(item,index) in videos" class="img-li">
          <!--跳转到视频-->
          <router-link target="_blank" :to="{path:'/video',query:{articleId:item.id}}">
            <a>
              <div class="img-wrapper">
                <div class="img-div">
                  <img class="img-css" :src="dynamicUrl+item.img[0]">
                  <div class="play-icon">
                    <img src="../../../../../assets/img/icon/播放.svg">
                  </div>
                  <div class="playIconWrapper"></div>
                </div>
              </div>
              <p class="title">{{item.title}}</p>
            </a>
          </router-link>
        </li>

      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      videos: Array,
      PRIFIX: String,
      dynamicUrl: String
    },
    data() {
      return {
        flag: false
      }
    },
    methods: {
      overShow() {
        this.flag = true
      },
      outHide() {
        this.flag = false
      }
    }
  }

</script>

<style lang="less" scoped>
  .cc{
    background-color: #f8f8f9;
  }

  .hi{
    font-weight: 700;
  }

  .img {
    display: flex;
    width: 306px;
    margin-top: 10px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .img-li {
    display: flex;
    max-width: 144px;
    margin-bottom: 5px;
  }

  .img-li a {
    display: inline-block;
    width: 144px;
  }

  .img-wrapper {
    display: inline-block;
    position: relative;
    width: 144px;
    height: 84px;
    overflow: hidden;
    border-radius: 5px;
  }

  .img-div {
    width: 144px;
    height: 84px;
    overflow: hidden;
    border-radius: 5px;
  }

  .img-css {
    position: absolute;
    width: 144px;
    height: 84px;
    overflow: hidden;
    border-radius: 5px;
    transition: all 0.6s;
    background-repeat: no-repeat;
    background-size: 100%;
    z-index: 1;
  }

  .img-div:hover .img-css {
    border-radius: 5px;
    cursor: pointer;
    transform: scale(1.05);
  }

  .img-div:hover .playIconWrapper {
    opacity: 1;
  }

  .play-icon {
    position: absolute;
    margin-left: 52px;
    margin-top: 22px;
    z-index: 3;
  }

  .playIconWrapper {
    position: absolute;
    width: 144px;
    height: 84px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 0;
    z-index: 2;
    transition: all 0.6s;
  }

  .title {
    font-size: 14px;
    width: 144px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }


</style>



