<template>
  <div>
    <Card style="width: 340px;margin-top: 10px" class="cc">
      <div class="model2">
        <h3 style="font-weight: 700;color: #000">
          更多
        </h3>
        <div class="content">
          <p style="margin-top: 5px;margin-bottom: 5px;color: #000;width:100%">
            <router-link to="/about">
              <a>关于我们</a>
            </router-link>
          </p>
          <p style="margin-top: 5px;margin-bottom: 5px">中国互联网举报中心 电话:12377</p>
          <p style="margin-top: 5px;margin-bottom: 5px">网络文化经营许可证 电信增值业务许可证</p>
          <p class="hh" style="margin-top: 5px;margin-bottom: 5px" @click="go">广播电视节目制作经营许可证</p>
          <p style="margin-top: 5px;margin-bottom: 5px">违法和不良信息举报：028-962377</p>
          <p style="margin-top: 5px;margin-bottom: 5px">公司名称：成都益豪娱创文化传媒有限公司</p>
          <p style="margin-top: 5px;margin-bottom: 5px">备案号：蜀ICP备18018817号-1</p>

        </div>
      </div>
    </Card>
    <Card style="width: 340px;margin-top: 10px" class="cc">
      <div class="model2">
        <h2>
          友情链接
        </h2>
        <div class="content">
          <div v-for="item in link" class="link-item">
            <a :href="item.link" target="_blank">{{item.linkDesc}}</a>
          </div>
        </div>
      </div>
    </Card>
  </div>

</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      about: Object,
      PRIFIX: String
    },
    data () {
      return {
        link: Array
      }
    },
    methods: {
      go () {
        this.$router.push('/certificate')
      }
    },
    created () {
      var qs = require('qs')
      var self = this
      this.$http.post(this.PRIFIX + '/oa/link/web/list',qs.stringify({
      })).then(function (response) {
        response = response.data
        if (response.code === 0) {
          self.link = response.data.link
        } else if(response.code === 3) {
          self.$Notice.error({
            title: '请登录',
          })
          let url = window.location.href + ''
          saveURL(url)
          setTimeout(function () {
            self.$router.push('/login')
          },500)
        } else {
          self.$Notice.error({
            title: '内容获取失败',
          })
        }
      }).catch(function (error) {})
    }
  }

</script>

<style lang="less" scoped>
  .cc {
    background-color: #f8f8f9;
  }

  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 10px;
  }

  .link-item a {
    margin-right: 10px;
  }

  .hh:hover {
    cursor: pointer;
  }
</style>



