<template>
  <div class="body">
    <div style="margin-bottom: 5px" v-for="(item,index) in article">
      <div v-if="index === 10" class="demo-affix">
        <a @click="showNewArticle" style="color: #ffffff">上次浏览到这里,点击刷新<Icon type="refresh"></Icon></a>
      </div>
      <div v-if="item.type==2">
        <Model1 :userInfo="userInfo" :content="item" :collect="collect" :dynamicUrl="dynamicUrl" :PRIFIX="PRIFIX" style="width: 700px"></Model1>
      </div>
      <div v-if="item.type==1">
        <Model2 :userInfo="userInfo" :content="item" :collect="collect" :dynamicUrl="dynamicUrl" :PRIFIX="PRIFIX" style="width: 700px"></Model2>
      </div>
      <div v-if="item.type==3">
        <Model3 :userInfo="userInfo" :content="item" :collect="collect" :dynamicUrl="dynamicUrl" :PRIFIX="PRIFIX" style="width: 700px"></Model3>
      </div>
    </div>
    <div v-if="contentFlag">
      <noContent></noContent>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Model1 from "./model1/model1.vue"
  import Model2 from "./model2/model2.vue"
  import Model3 from "./model3/model3.vue"
  import noContent from "./noContent/noContent.vue"

  export default{
    props: {
      userInfo: Object,
      article: Array,
      PRIFIX: String,
      contentFlag: Boolean,
      dynamicUrl: String
    },
    data() {
      return {
        scrollDisable: false,
        collect: false,
        showNew: false
      }
    },
    components: {
      Model1,
      Model2,
      Model3,
      noContent
    },
    methods: {
      showNewArticle() {
        this.$emit('showNewArticle')
      }
    }
  }

</script>

<style lang="less" scoped>
  .body {
    margin-top: 10px;
    width: 100%;
    border-radius: 5px;
  }

  @keyframes myRotating
  {
    from {
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
    }
  }

  @-webkit-keyframes myRotating /* Safari and Chrome */
  {
    from {
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
    }
  }

  .demo-affix{
    color: #fff;
    padding: 6px;
    text-align: center;
    background: rgba(96,163,245,.85);
    width: 700px;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-top: 5px;
    font-size: 14px;
  }

</style>



