<template>
  <Card>
    <div class="title">
      <h2 v-if="comment.commentNum==null">0条评论</h2>
      <h2 v-else>{{comment.commentNum}}条评论</h2>
    </div>
    <!--用户信息-->
    <div class="userComment">
      <span v-if="userFlag" class="avatar">
        <img src="../../../../../static/img/boy.svg">
      </span>
      <span v-else class="avatar">
        <img :src="userInfo.avatar">
      </span>
      <div class="commentInput">
        <Input v-model="myComment" type="textarea" @on-focus="changeSize1(4)" :rows="rows1" placeholder="请输入您的评论">
        </Input>
      </div>
      <div class="commentButton" @click="sendMyComment">
        <Button type="info">评论</Button>
      </div>
    </div>
    <div>
      <!--用户评论-->
      <div v-for="(item,index) in comment.pageContent" class="otherComment">
        <div class="otherAvatar">
          <router-link :to="{path:'/otherPersonInfo',query:{idUser:item.userID}}">
          <img :src="item.avatar">
          </router-link>
        </div>
        <div class="otherUser">
          <div class="other">
            <router-link :to="{path:'/otherPersonInfo',query:{idUser:item.userID}}">
              <a>{{item.username}}</a>
            </router-link>
            <span>{{item.time}}</span>
          </div>
          <div class="content">
            {{item.commentInfo}}
          </div>
          <div class="foot">
            <a v-if="userInfo.id != item.userID" class="right1" @click="reply1(index)">回复</a>
            <span v-if="item.replyNum!=0">
            <a class="right1" v-show="!selectReply(index)" @click="showReply(index,item.id)">
              {{item.replyNum}}条回复
              <Icon type="chevron-down"></Icon>
            </a>
            <a class="right1" v-show="selectReply(index)" @click="hideReply">
              收起回复
              <Icon type="chevron-up"></Icon>
            </a>
          </span>
            <p class="f-right">
            <span v-if="item.goodFlag==0" class="ic" @click="addGoodA(index,item.id)">
              {{item.good}}
              <Icon class="right1" type="thumbsup"></Icon>
            </span>
              <span v-if="item.goodFlag!=0" class="good">
              {{item.good}}
              <Icon class="right1" type="thumbsup"></Icon>
            </span>
              <span @click="commentShare2(item)">
              <Icon class="right1 ic" type="ios-information-outline"></Icon>
            </span>
            </p>
          </div>
          <!--回复-->
          <div class="reply" v-show="replyFlagA(index)">
            <Input v-model="sendReplyContent.replyInfo" type="textarea" :autosize="{minRows: 1,maxRows: 4}"
                   placeholder="请输入您的评论"></Input>
            <div class="replyButton">
              <Button type="info" @click="replyContent(item,index,item);showReply(index,item.id)">回复</Button>
              <Button type="ghost" @click="closeReply1(index)">关闭</Button>
            </div>
          </div>
          <!--回复评论-->
          <div v-if="item.reply.pageContent" v-for="(itemA,indexA) in item.reply.pageContent"
               class="replyContent" v-show="selectReply(index)">
            <div class="otherComment">
              <div class="otherAvatar">
                <router-link :to="{path:'/otherPersonInfo',query:{idUser:itemA.userID}}">
                <img :src="itemA.avatar">
                </router-link>
              </div>
              <div class="otherUser">
                <div class="other">
                  <router-link :to="{path:'/otherPersonInfo',query:{idUser:itemA.userID}}">
                    <a>{{itemA.username}}</a>
                  </router-link>
                  <span>{{itemA.time}}</span>
                </div>
                <div v-if="item.id==itemA.byReplyId" class="content">
                  {{itemA.replyInfo}}
                </div>
                <div v-else class="content">
                  {{itemA.replyInfo}} //@{{itemA.byUsername}}:{{itemA.byReplyInfo}}
                </div>
                <div class="foot" style="width: 606px;">
                  <a v-if="userInfo.id != itemA.userID" class="right1" @click="reply2(indexA)">回复</a>
                  <p class="f-right">
                  <span v-if="itemA.goodFlag==0" class="ic" @click="addGoodB(index,indexA,itemA.id)">
                    {{itemA.good}}
                    <Icon class="right1" type="thumbsup"></Icon>
                  </span>
                  <span v-else class="good">
                    {{itemA.good}}
                    <Icon class="right1" type="thumbsup"></Icon>
                  </span>
                    <span @click="commentShare(itemA)">
                    <Icon class="right1 ic" type="ios-information-outline"></Icon>
                  </span>
                  </p>
                </div>
                <!--回复-->
                <div class="reply" v-show="replyFlagB(indexA)">
                  <Input v-model="sendReplyContent.replyInfo" type="textarea" :autosize="{minRows: 1,maxRows: 4}"
                         placeholder="请输入您的评论"></Input>
                  <div class="replyButton">
                    <Button type="info" @click="replyContent(itemA,index,item)">回复</Button>
                    <Button type="ghost" @click="closeReply2(indexA)">关闭</Button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="rmo" class="more">
            <p @click="showMoreReply(item.id)">点击显示更多回复</p>
          </div>
        </div>
      </div>
      <div v-if="commentLoadNum" class="more">
        <p @click="showMoreComment()">点击显示更多评论</p>
      </div>
    </div>

    <Modal class="report" v-model="modal1" @on-ok="ok" @on-cancel="cancel">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>举报此人</span>
      </p>
      <RadioGroup v-model="vertical" vertical>
        <Radio v-for="item in report" :label="item.reportId">
          <span>{{item.reportType}}</span>
        </Radio>
        <Input v-model="content1" style="width: 488px;margin-top: 10px;" type="textarea" :rows="4"
               placeholder="我有话说..."></Input>
      </RadioGroup>
    </Modal>
  </Card>
</template>

<script type="text/ecmascript-6">
  import {loadFromLocal} from "../../../../../src/common/js/store.js"
  export default {
    props: {
      comment: Object,
      userInfo: Object,
      PRIFIX: String,
      articleId: Number,
      commentLoadNum: Boolean,
      rmo: Boolean,
      dynamicUrl: String
    },
    data() {
      return {
        userFlag: true,
        share: 0,
        modal1: false,
        content1: "",
        vertical: -1,
        replyFlag: true,
        selectReplyIndex: -1,
        index1: -1,
        index2: -1,
        rows1: 1,
        myComment: '',
        replyGood: -1,
        sendReplyContent: {
          id: -1,
          rid: -1, //被回复者的id
          byReplyId: -1,//被回复的id
          replyInfo: "",//回复的信息
          byReplyInfo: "",//被回复的信息
          username: "",//回复的用户名
          byUsername: "",//被回复的用户名
          index: -1,
          userID: -1
        },
        shareComment: {
          rid: -1,
          byReplyId: -1
        }
      }
    },
    methods: {
      addGoodA(index, id){
        this.$emit("addGoodA", index, id)
      },
      addGoodB(index1, index2, id){
        this.$emit("addGoodB", index1, index2, id)
      },
      reply1(index) {
        this.index1 = index
        this.index2 = -1
      },
      replyContent(itemA, index, item) {
        this.sendReplyContent.id = item.id
        this.sendReplyContent.rid = itemA.userID
        this.sendReplyContent.byReplyId = itemA.id
        this.sendReplyContent.byUsername = itemA.username
        this.sendReplyContent.index = index
        this.sendReplyContent.byReplyInfo = itemA.replyInfo
        this.sendReplyContent.userID = this.userInfo.id
        this.$emit("replyContent", this.sendReplyContent)
        /*清空*/
        this.sendReplyContent = {
          id: -1,
          rid: -1,
          byReplyId: -1,//被回复的id
          replyInfo: "",//回复的信息
          byReplyInfo: "",//被回复的信息
          username: "",//回复的用户名
          byUsername: "",//被回复的用户名
          index: -1,
          userID: -1
        }
        this.closeReply1(index)
        this.closeReply2(index)
      },
      replyFlagA(index) {
        if (this.index1 == index) {
          return true
        }
      },
      reply2(index) {
        this.index1 = -1
        this.index2 = index
      },
      replyFlagB(index) {
        if (this.index2 == index) {
          return true
        }
      },
      closeReply1(index) {
        this.index1 = -1
      },
      closeReply2(index) {
        this.index2 = -1
      },
      changeSize1(num) {
        this.rows1 = num
      },
      showReply(index, id) {
        this.selectReplyIndex = index
        this.replyFlag = false
        this.replyGood = index
        this.$emit("selectReplyContent", index, id)
      },
      hideReply() {
        this.selectReplyIndex = -1
        this.replyFlag = true
      },
      loginFlag() {
        if (this.userInfo.id == '' || this.userInfo.id == null) {
          return true
        } else {
          return false
        }
      },
      sendMyComment() {
        this.changeSize1(1)
        if (this.myComment == "" || this.myComment == null) {
          this.$Message.error('不能发布空内容！');
        } else {
          this.$emit("sendMyComment", this.myComment)
        }
        this.myComment = ""
      },
      selectReply(index) {
        if (this.selectReplyIndex == index) {
          return true
        } else {
          return false
        }
      },
      commentShare(item) {
        this.modal1 = true
        this.shareComment.rid = item.userID
        this.shareComment.byReplyId = item.id
        this.share = 1
      },
      commentShare2(item) {
        this.modal1 = true
        this.shareComment.rid = item.userID
        this.shareComment.byReplyId = item.id
        this.share = 2
      },
      ok() {
        if (this.share == 1) {
          this.$emit("commentShare", this.vertical, this.content1, this.shareComment)
        }

        if (this.share == 2) {
          this.$emit("commentShare2", this.vertical, this.content1, this.shareComment)
        }

        this.share = 0
        this.vertical = ""
        this.content1 = ""

      },
      cancel() {
        this.share = 0
        this.vertical = ""
        this.content1 = ""
      },
      showMoreReply(id) {
        this.$emit('showMoreReply',id)
      },
      showMoreComment() {
        this.$emit('showMoreComment')
      }
    },
    created() {
      this.$nextTick(() => {
        if(this.userInfo.flag) {
          this.userFlag = false
        }
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/article/report/type/list').then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.report = response.data.reportTypeList
          }
        }).catch(function (error) {})
      })

    }
  }

</script>

<style lang="less" scoped>
  .userComment {
    display: table;
  }

  .title {
    margin-bottom: 10px;
  }

  .avatar img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    vertical-align: middle;

  }

  .commentInput {
    display: inline-block;
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 636px;
  }

  .ivu-input-wrapper > textarea {
    resize: none;
  }

  .commentButton {
    float: right;
  }

  .otherComment {
    display: table;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #f5f5f5;
    border-radius: 10px;
  }

  .otherAvatar {
    width: 28px;
    height: 28px;
  }

  .otherAvatar img {
    width: 28px;
    height: 28px;
    border-radius: 50%;
  }

  .otherAvatar {
    display: table-cell;
    vertical-align: top;
  }

  .other {
    margin-left: 10px;
    font-size: 14px;
  }

  .other a {
    color: #406599;
  }

  .content {
    margin-left: 10px;
    font-size: 14px;
    line-height: 22px;
    color: #222;
  }

  .foot {
    width: 636px;
    margin-left: 10px;
    margin-top: 6px;
    font-size: 14px;
  }

  .foot a {
    color: #406599;
  }

  .right1 {
    margin-right: 10px;
  }

  .f-right {
    float: right;
    right: 0px;
    bottom: 0px;
  }

  .good {
    font-size: 14px;
    color: #ed4040;
  }

  .good:hover {
    cursor: default;
  }

  .ic {
    font-size: 14px;
  }

  .ic:hover {
    color: #ed4040;
    cursor: pointer;
  }

  .ic:active {
    color: #ed4040;
    animation: bounceIn 0.1s;
  }

  @keyframes bounceIn {
    0% {
      font-size: 14px;
    }
    50% {
      font-size: 18px;
    }
    100% {
      font-size: 14px;
    }
  }

  @-webkit-keyframes myShow /*Safari and Chrome*/
  {
    0% {
      font-size: 14px;
    }
    50% {
      font-size: 18px;
    }
    100% {
      font-size: 14px;
    }
  }

  .reply {
    margin-left: 10px;
    opacity: 1;
    animation: mymove 1s;
    -webkit-animation: myShow 1s; /*Safari and Chrome*/
  }

  @keyframes myShow {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @-webkit-keyframes myShow /*Safari and Chrome*/
  {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .replyButton {
    margin-top: 5px;
    margin-bottom: 5px;
    float: right;
  }

  .more{
    text-align: center;
    clear: both;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .more p{
    color: #2d8cf0;
  }

  .more p:hover{
    cursor: pointer;
  }

  .moreNO {
    text-align: center;
    clear: both;
    margin-top: 5px;
    margin-bottom: 5px;
    color: #bbbec4;
  }
</style>



