export function saveToLocal(id,name,avatar,qq,sex,email,city,birthday,signatrue,backgroundImg,
                            phone,fansCounts,followCounts,flag)
{
  if(!window.localStorage){
    alert("浏览器不支持localstorage");
  }else{
    var user = window.localStorage;
    var data = {
      id: id,
      name: name,
      avatar: avatar,
      flag: flag,
      QQ: qq,
      sex: sex,
      email: email,
      city: city,
      birthday: birthday,
      signature: signatrue,
      backgroundImg: backgroundImg,
      phone: phone,
      fans: fansCounts,
      attention:followCounts

    };
    var d = JSON.stringify(data);
    user.setItem("data",d);
  }
}
export function readLocal() {
  var user = window.localStorage;
  //将JSON字符串转换成为JSON对象输出
  var json = user.getItem("data");
  var jsonObj = JSON.parse(json);
  return jsonObj
}

export function loadFromLocal() {
  var user = window.localStorage;
  //将JSON字符串转换成为JSON对象输出
  var json = user.getItem("data");
  var jsonObj = JSON.parse(json);
  if(jsonObj.flag) {
    return true
  } else {
    return false
  }
}

export function clearToLocal() {
  if(!window.localStorage){
    alert("浏览器支持localstorage");
  }else{
    var user = window.localStorage;
    var data = {
      name: "",
      avatar: "",
      attention: "",
      fans: "",
      flag: false
    };
    var d = JSON.stringify(data);
    user.setItem("data",d);
  }
}

export function saveURL(url) {
  if(!window.localStorage){
    alert("浏览器不支持localstorage");
  }else{
    var lsURL = window.localStorage;
    var urlArr = url.split("/")
    var u = ''
    for(let i =3;i< urlArr.length;i++) {
      if(i == urlArr.length-1) {
        u += urlArr[i]
      } else {
        u += urlArr[i]+"/"
      }
    }
    lsURL.setItem("url",u);
  }
}

export function loadURL() {
  var lsURL = window.localStorage;
  var json = lsURL.getItem("url");
  return json
}

export function saveNav(val) {
  if(!window.localStorage){
    alert("浏览器不支持localstorage");
  }else{
    var nav = window.localStorage;
    var navD = JSON.stringify(val);
    nav.setItem("navigation",navD);
  }
}

export function loadNav() {
  var nav = window.localStorage;
  //将JSON字符串转换成为JSON对象输出
  var json = nav.getItem("navigation");
  var jsonObj = JSON.parse(json);
  return jsonObj
}

export function saveNavCss(val) {
  if(!window.localStorage){
    alert("浏览器不支持localstorage");
  }else{
    var navCss = window.localStorage;
    navCss.setItem("navigationCss",val);
  }
}

export function loadNavCss() {
  var nav = window.localStorage;
  //将JSON字符串转换成为JSON对象输出
  var json = nav.getItem("navigationCss");
  return json
}


