<template>
  <div>
    <Card v-if="attentionFlag">
      <div class="attention">
        <div class="attention-item">
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.fid}}">
            <div class="avatar">
              <img :src="attentionPerson.byAvatar">
            </div>
          </router-link>
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.fid}}">
            <div class="name">
              <a>{{attentionPerson.byUsername}}</a>
            </div>
          </router-link>
        </div>
        <div class="button">
          <span v-if="attentionPerson.eachOtherAttention!=0">
            <Tooltip content="点击取消关注" placement="top">
              <Button icon="arrow-swap" @click="attentionNO1">互相关注</Button>
            </Tooltip>
          </span>
          <Button v-else type="warning" icon="minus" @click="attentionNO1">取消关注</Button>
          <Badge v-if="attentionPerson.unread != 0" :count="attentionPerson.unread" overflow-count="99">
            <Button type="ghost" icon="ios-email" @click="showPrivateLetterModal1">私信</Button>
          </Badge>
          <Button v-if="attentionPerson.unread == 0" type="ghost" icon="ios-email" @click="showPrivateLetterModal1">私信</Button>
        </div>
      </div>
    </Card>
    <Card v-else>
      <div class="attention">
        <div class="attention-item">
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.aid}}">
            <div class="avatar">
              <img :src="attentionPerson.byAvatar">
            </div>
          </router-link>
          <router-link target="_blank" :to="{path:'/otherPersonInfo',query:{idUser:attentionPerson.aid}}">
            <div class="name">
              <a>{{attentionPerson.byUsername}}</a>
            </div>
          </router-link>
        </div>
        <div class="button">
          <Button v-if="attentionPerson.eachOtherAttention!=0" icon="arrow-swap"
                  @click="attentionNO2">互相关注</Button>
          <Button v-else type="primary" icon="plus" @click="attentionYES">关注</Button>
          <Badge v-if="attentionPerson.unread != 0" :count="attentionPerson.unread" overflow-count="99">
            <Button type="ghost" icon="ios-email" @click="showPrivateLetterModal2">私信</Button>
          </Badge>
          <Button v-if="attentionPerson.unread == 0" type="ghost" icon="ios-email" @click="showPrivateLetterModal2">私信</Button>
        </div>
      </div>
    </Card>
  </div>

</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      modal: Boolean,
      attentionPerson: Object,
      attentionFlag: Boolean,
      index: Number,
      PRIFIX: String,
      dynamicUrl: String
    },
    methods: {
      showPrivateLetterModal1() {
        this.$emit("showPrivateLetterModal1",this.attentionPerson.fid,this.index)
      },
      showPrivateLetterModal2() {
        this.$emit("showPrivateLetterModal2",this.attentionPerson.aid,this.index)
      },
      attentionYES() {
        let data1 = this.index
        let data2 = this.attentionPerson
        this.$emit("attentionYES",data1,data2)
      },
      attentionNO1() {
        let data1 = this.index
        this.$emit("attentionNO1",data1,this.attentionPerson)
      },
      attentionNO2() {
        let data1 = this.index
        this.$emit("attentionNO2",data1,this.attentionPerson)
      }
    }
  }

</script>

<style lang="less" scoped>

  .attention {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex: 1;
  }

  .attention-item {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .avatar img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
  }

  .avatar img:hover {
    cursor: pointer;
  }

  .name {
    font-size: 16px;
    margin-left: 20px;
  }


</style>



