<template>
  <Card style="width: 340px;margin-top: 10px" class="cc">
    <div class="model3">
      <!--跳转到图片-->
      <router-link target="_blank" :to="{path:'sortPageType',query:{navType:2}}">
        <h4 style="color: #000000" class="hi">
          精彩图片
        </h4>
      </router-link>
      <ul class="img">
        <li v-for="(item,index) in imgs" class="img-li">
          <!--跳转到图片-->
          <router-link target="_blank" :to="{path:'/imgs',query:{articleId:item.id}}">
            <a>
              <div class="img-div">
                <img :src="dynamicUrl+item.img[0]">
                <i class="more">{{item.imgNum}}图</i>
              </div>
              <p class="title">{{item.title}}</p>
            </a>
          </router-link>
        </li>
      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">

  export default {
    props: {
      imgs: Array,
      PRIFIX: String,
      dynamicUrl: String
    }
  }

</script>

<style lang="less" scoped>
  .cc{
    background-color: #f8f8f9;
  }

  .img {
    display: flex;
    width: 306px;
    margin-top: 10px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .hi{
    font-weight: 700;
  }

  .img-li {
    display: flex;
    max-width: 144px;
    margin-bottom: 5px;
  }

  .img-li a {
    display: inline-block;
    width: 144px;
  }

  .img-div {
    display: inline-block;
    position: relative;
    border-radius: 5px;
    width: 144px;
    height: 84px;
    overflow: hidden;
    text-align: center;
  }

  .img-div img {
    border-radius: 5px;
    display: inline-block;
    min-width: 144px;
    max-height: 84px;
    transition: all 0.6s;
  }

  .img-div img:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  .more {
    position: absolute;
    font-style: normal;
    right: 8px;
    bottom: 6px;
    padding: 0 6px;
    border-radius: 10px;
    background: #000;
    filter: alpha(opacity=70);
    background: rgba(0, 0, 0, .7);
    color: #ffffff;
  }

  .title {
    font-size: 14px;
    width: 144px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }


</style>



