<template>
  <div class="all">
    <div class="top_wrapper">
      <Card style="width: 100%;" :bordered="false">
        <div class="top">
          <Input @keyup.enter="searchPage" v-model="value" placeholder="输入搜索..." size="large" style="width: 92%"></Input>
          <Button size="large" type="ghost" @click="searchPage">
            <Icon type="search"></Icon>
            搜索
          </Button>

          <div class="hot">
            <span>热搜关键词:</span>
            <span v-for="item in hotSearch">
              <a @click="searchHot(item.content)">{{item.content}}</a>
            </span>
          </div>
        </div>
      </Card>
    </div>

    <div class="search" ref="elSearch">
      <div class="left">
        <div v-if="searchFlag">
          <Tabs value="name1">
            <TabPane label="文章" name="name1">
              <div v-for="item in search" v-if="item.type==1">
                <Bmodel2 :PRIFIX="PRIFIX" :content="item" :dynamicUrl="dynamicUrl" :collect="false"></Bmodel2>
              </div>
              <div v-if="loadCssFlag" class="demo-spin-col">
                <Spin fix>
                  <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                  <div>Loading</div>
                </Spin>
              </div>
            </TabPane>
            <TabPane label="多图" name="name2">
              <div v-for="item in search" v-if="item.type==2">
                <Bmodel1 :PRIFIX="PRIFIX" :content="item" :dynamicUrl="dynamicUrl" :collect="false"></Bmodel1>
              </div>
              <div v-if="loadCssFlag" class="demo-spin-col">
                <Spin fix>
                  <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                  <div>Loading</div>
                </Spin>
              </div>
            </TabPane>
            <TabPane label="视频" name="name3">
              <div v-for="item in search" v-if="item.type==3">
                <Bmodel3 :PRIFIX="PRIFIX" :content="item" :dynamicUrl="dynamicUrl" :collect="false"></Bmodel3>
              </div>
              <div v-if="loadCssFlag" class="demo-spin-col">
                <Spin fix>
                  <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                  <div>Loading</div>
                </Spin>
              </div>
            </TabPane>
          </Tabs>
        </div>
        <div v-if="!searchFlag">
          <h4 class="search-label">~~~客官，没有搜到内容哦~~~</h4>
        </div>
      </div>
      <div class="right">
        <Model1 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :realTimeNews="recommendList.realTimeNews" style="margin-top: 10px"></Model1>
        <Model3 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :imgs="recommendList.recommendImgs" style="margin-top: 10px"></Model3>
        <Model4 :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl" :videos="recommendList.recommendVideos" style="margin-top: 10px"></Model4>
      </div>
    </div>
    <UserLogin :PRIFIX="PRIFIX" :userInfo="userInfo" :dynamicUrl="dynamicUrl"></UserLogin>
    <BackTop></BackTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bmodel1 from "../index/components/body/model1/model1.vue"
  import Bmodel2 from "../index/components/body/model2/model2.vue"
  import Bmodel3 from "../index/components/body/model3/model3.vue"
  import Model1 from "../index/components/recommendModel/model1/model1.vue"
  import Model3 from "../index/components/recommendModel/model3/model3.vue"
  import Model4 from "../index/components/recommendModel/model4/model4.vue"
  import UserLogin from "../common/components/userLogin/userLogin.vue"
  import {readLocal} from "../../../src/common/js/store.js"

  export default {
    props: {
      PRIFIX: String,
      userInfo: Object,
      recommendList: Object,
      searchVal: String,
      dynamicUrl: String
    },
    watch: {
      searchVal: function () {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/homePage/search', qs.stringify({
          'content': self.searchVal.replace(/%08/g,"")
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.search = response.data.article.pageContent
            if(self.search.length == 0) {
              self.searchFlag = false
            } else {
              self.searchFlag = true
              self.contentFlag = true
            }
          } else {
            self.searchFlag = false
            self.$Notice.info({
              title: "没有搜索到~",
            })
          }
        }).catch(function (error) {})
      }
    },
    data () {
      return {
        contentFlag: false,
        searchFlag: true,
        value: '',
        search: [],
        hotSearch: [],
        loadCssFlag: false
      }
    },
    components: {
      Bmodel1,
      Bmodel2,
      Bmodel3,
      Model1,
      Model3,
      Model4,
      UserLogin
    },
    methods: {
      searchPage() {
        if(this.value == '') {
          self.$Notice.error({
            title: "搜索不能为空哦~",
          })
        }else {
          var qs = require('qs')
          var self = this
          this.$http.post(this.PRIFIX + '/api/homePage/search', qs.stringify({
            'content': self.value.replace(/%08/g,"")
          })).then(function (response) {
            response = response.data
            if (response.code == 0) {
              self.search = response.data.article.pageContent
              if(self.search.length == 0) {
                self.searchFlag = false
              } else {
                self.searchFlag = true
                self.contentFlag = true
              }
            } else {
              self.searchFlag = false
              self.$Notice.error({
                title: response.message,
              })
            }
          }).catch(function (error) {})
        }
      },
      searchHot(val) {
        this.searchVal = val
        this.value = val
      }
    },
    created() {
      this.userInfo = readLocal()
      this.value = this.$route.query.search
      if(this.value == '') {
        self.$Notice.error({
          title: "搜索不能为空哦~",
        })
      } else {
        var qs = require('qs')
        var self = this
        this.$http.post(this.PRIFIX + '/api/homePage/search', qs.stringify({
          'size': 10,
          'content': self.value.replace(/%08/g,"")
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            self.search = response.data.article.pageContent
            if(self.search.length == 0) {
              self.searchFlag = false
            } else {
              self.searchFlag = true
            }
          } else {
            self.searchFlag = false
            self.$Notice.error({
              title: "内容获取失败",
            })
          }
        }).catch(function (error) {})
      }
      /*热搜关键词*/
      var self = this
      this.$http.post(this.PRIFIX + '/api/homePage/hot/search/words').then(function (response) {
        response = response.data
        if (response.code == 0) {
          self.hotSearch = response.data.hot
        } else {
          self.$Notice.error({
            title: response.message,
          })
        }
      }).catch(function (error) {})
    },
    mounted () {
      var i = 1
      this.$nextTick(() => {
        window.addEventListener('scroll', () => {
          /*内容高度*/
          let elHeight = this.$refs.elSearch.clientHeight + 80 + 96
          /*滚动高度*/
          let height = 0
          if (document.body.scrollTop) {
            height = document.body.scrollTop
          }
          if (document.documentElement.scrollTop) {
            height = document.documentElement.scrollTop
          }
          if (height + window.innerHeight >= elHeight) {
            if(!this.contentFlag) {
              /*推送文章*/
              var qs = require('qs')
              var self = this
              self.loadCssFlag = true
              this.$http.post(this.PRIFIX + '/api/homePage/search', qs.stringify({
                'size': 6,
                'page': i,
                'content': self.value
              })).then(function (response) {
                response = response.data
                if (response.code == 0) {
                  if(response.data.article.pageContent == 0) {
                    self.contentFlag = true
                    self.loadCssFlag = false
                  } else {
                    response.data.article.pageContent.forEach((item) => {
                      self.search.push(item)
                    })
                    self.loadCssFlag = false
                  }
                } else {
                  self.$Notice.error({
                    title: "内容获取失败",
                  })
                  self.loadCssFlag = false
                }
              }).catch(function (error) {})
              i++
            }
          }
        })
      })
    },
  }

</script>

<style lang="less" scoped>

  .search {
    display: flex;
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .search-label{
    text-align: center;
    color: #bbbec4;
    margin-top: 30px;
  }

  .left {
    flex: 0 0 710px;
    width: 710px;
    margin-right: 20px;
  }

  .right {
    flex: 1;
    width: 340px;
  }

  .top_wrapper {
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .demo-auto-complete-item {
    padding: 4px 0;
    border-bottom: 1px solid #F6F6F6;
  }

  .demo-auto-complete-group {
    font-size: 12px;
    padding: 4px 6px;
  }

  .demo-auto-complete-group span {
    color: #666;
    font-weight: bold;
  }

  .demo-auto-complete-group a {
    float: right;
  }

  .demo-auto-complete-count {
    float: right;
    color: #999;
  }

  .demo-auto-complete-more {
    display: block;
    margin: 0 auto;
    padding: 4px;
    text-align: center;
    font-size: 12px;
  }

  .hot {
    margin-top: 10px;
    font-size: 13px;
  }

  .hot a {
    margin-left: 10px;
    margin-right: 10px;
  }


</style>



