<template>
  <div class="labelClass">
    <Icon class="right1" type="ios-pricetags-outline"></Icon>
    <Breadcrumb class="bspan">
      <BreadcrumbItem v-for="(item,index) in keyword" :key="item.id" style="font-weight: 400;">
        <a style="color: #406599;">{{item}}</a>
      </BreadcrumbItem>
    </Breadcrumb>
    <div class="descriptionLabel">
      <span>
        <span v-if="collection==0" class="des" @click="collectArticleYES">
          <Icon class="font left1" type="ios-star-outline"></Icon>收藏
        </span>
        <span v-else class="des yes" @click="collectArticleNO">
          <Icon class="font left1" type="ios-star"></Icon>已收藏
        </span>
        <span class="des" @click="modal1 = true">
          <Icon class="font left1" type="ios-information-outline"></Icon>举报
        </span>
      </span>
      <span></span>
    </div>
    <!--举报-->
    <Modal class="report" v-model="modal1" @on-ok="ok" @on-cancel="cancel">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>举报文章</span>
      </p>
      <RadioGroup v-model="type" vertical>
        <!--<Radio label="1">
          <span>淫秽色情</span>
        </Radio>
        <Radio label="2">
          <span>营销广告</span>
        </Radio>
        <Radio label="3">
          <span>恶意攻击谩骂</span>
        </Radio>-->
        <Radio v-for="item in report" :label="item.reportId">
          <span>{{item.reportType}}</span>
        </Radio>
        <Input v-model="content" style="width: 488px;margin-top: 10px;" type="textarea" :rows="4" placeholder="我有话说..."></Input>
      </RadioGroup>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">

  export default{
    props: {
      keyword: Array,
      collection: Number,
      PRIFIX: String
    },
    data() {
      return {
        modal1: false,
        type: "",
        content: ""
      }
    },
    methods:{
      collectArticleYES() {
        this.$emit("collectArticleYES")
      },
      collectArticleNO() {
        this.$emit("collectArticleNO")
      },
      ok() {
        this.$emit("share",this.type,this.content)
        this.type = ""
        this.content = ""
      },
      cancel() {
        this.type = ""
        this.content = ""
      }
    },
    created() {
      var qs = require('qs')
      var self = this
      this.$http.post(this.PRIFIX + '/api/article/report/type/list').then(function (response) {
        response = response.data
        if (response.code === 0) {
          self.report = response.data.reportTypeList
        }
      }).catch(function (error) {})
    }
  }

</script>

<style lang="less" scoped>
  .labelClass{
    font-size: 14px;
    text-align: left;
  }

  .right1{
    margin-right: 15px;
    color: #9e9e9e;
  }

  .descriptionLabel{
    display: inline-block;
    float: right;
  }

  .font{
    font-size: 16px;
  }

  .left1{
    margin-left: 5px;
    margin-right: 2px;
  }

  .des{
    color: #495060;
  }

  .des:hover{
    color: #ed4040;
    cursor: pointer;
  }

  .bspan{
    display: inline-block;
  }

  .yes{
    color: #ed4040;
  }
</style>



