/*首页*/
import home from "../views/index/index.vue"
/*图文*/
import imgText from "../views/article/imgText/imgText.vue"
import otherImgText from "../views/article/imgText/otherImgText.vue"
/*多图*/
import imgs from "../views/article/imgs/imgs.vue"
/*视频*/
import video from "../views/article/video/video.vue"
/*分类*/
import sortPage from "../views/sortPage/sortPage.vue"
import sortPageType from "../views/sortPage/sortPageType.vue"
/*搜索*/
import search from "../views/search/search.vue"
/*个人中心*/
import personInfo from "../views/personInfo/personInfo.vue"
/*修改个人信息*/
import updatePersonInfo from "../views/personInfo/updatePersonInfo.vue"
/*Ta人信息*/
import otherPersonInfo from "../views/personInfo/otherPersonInfo.vue"
/*关于我们*/
import about from "../views/about/about.vue"
/*看Ta说*/
import dynamic from "../views/dynamic/dynamic.vue"
/*许可证*/
import certificate from "../views/certificate/certificate.vue"

const routers = [
  {
    path: '/',
    meta: {
      title: '妙漫网创'
    },
    component: (resolve) => require(['../views/home.vue'], resolve),
  },
  {
    path: '/home',
    meta: {
      title: '妙漫网创'
    },
    component: (resolve) => require(['../views/home.vue'], resolve),
  },
  {
    path: '/index',
    meta: {
      title: '妙漫网创'
    },
    component: (resolve) => require(['../views/index.vue'], resolve),
    children: [
      {path: '/imgText', title: '妙漫网创', name: 'imgText', component: imgText},
      {path: '/otherImgText', title: '妙漫网创', name: 'otherImgText', component: otherImgText},
      {path: '/imgs', title: '妙漫网创', name: 'imgs', component: imgs},
      {path: '/video', title: '妙漫网创', name: 'video', component: video},
      {path: '/sortPage', title: '妙漫网创', name: 'sortPage', component: sortPage},
      {path: '/search', title: '妙漫网创', name: 'search', component: search},
      {path: '/personInfo', title: '妙漫网创', name: 'personInfo', component: personInfo},
      {path: '/updatePersonInfo', title: '妙漫网创', name: 'updatePersonInfo', component: updatePersonInfo},
      {path: '/otherPersonInfo', title: '妙漫网创', name: 'otherPersonInfo', component: otherPersonInfo},
      {path: '/about', title: '妙漫网创', name: 'about', component: about},
      {path: '/dynamic', title: '妙漫网创', name: 'dynamic', component: dynamic},
      {path: '/sortPageType', title: '妙漫网创', name: 'sortPageType', component: sortPageType},
      {path: '/certificate', title: '妙漫网创', name: 'certificate', component: certificate},
    ]
  },
  {
    path: '/login',
    meta: {
      title: '登录'
    },
    component: (resolve) => require(['../views/login.vue'], resolve)
  },
  {
    path: '/qqLogin',
    meta: {
      title: '登录'
    },
    component: (resolve) => require(['../views/qqLogin.vue'], resolve)
  },
  {
    path: '/weixinLogin',
    meta: {
      title: '登录'
    },
    component: (resolve) => require(['../views/weixinLogin.vue'], resolve)
  }
];
export default routers;




