<template>
  <Card class="model1Wrapper">
    <div class="model1">
      <h4 style="color: #000000" class="hi">
        精彩热闻
      </h4>
      <ul class="news">

        <li v-for="(item,index) in realTimeNews" class="news-item">
          <router-link target="_blank" :to="{path:'/imgText',query:{articleId:item.id}}">
            <a class="dd">
              <div class="left">
                <img :src="dynamicUrl+item.img[0]">
              </div>
              <div class="right">
                <p>{{item.title | exceedFont(item.title)}}</p>
              </div>
            </a>
          </router-link>
        </li>

      </ul>
    </div>
  </Card>
</template>

<script type="text/ecmascript-6">
  import {exceed2} from "../../../../../common/js/exceed"

  export default {
    props: {
      realTimeNews: Array,
      PRIFIX: String,
      dynamicUrl: String
    },
    filters: {
      exceedFont(text) {
        return exceed2(text)
      }
    }
  }
</script>

<style lang="less" scoped>

  .model1Wrapper {
    width: 340px;
    margin-top: 10px;
    background-color: #f8f8f9;
  }

  .hi{
    font-weight: 900;
  }

  .news {
    margin-top: 10px;
    list-style: none;
  }

  .news-item {
    margin-top: 6px;
    padding-bottom: 2px;
    border-bottom: 1px solid #ffffff;
  }

  .news-item a {
    display: table;
    overflow: hidden;
    color: #222222;
    height: 60px;
  }

  .left {
    position: relative;
    overflow: hidden;
    display: inline-block;
    text-align: center;
    width: 60px;
    height: 60px;
    margin-right: 12px;
  }

  .left img {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    transition: all 0.6s;
  }

  .left img:hover {
    transform: scale(1.02);
  }

  .right {
    height: 60px;
    display: table-cell;
    vertical-align: middle;
  }

  .right p {
    display: -webkit-box;
    font-size: 14px;
    line-height: 24px;
    width: 234px;
    overflow: hidden;
  }

  .dd{
    font-weight: 400;
    color: #000000;
  }

  .dd:hover{
    color: #406599;
  }

</style>



