var imgData = 0
var local = 0
export function compress(e) {
  /*压缩文件*/
  var file = e
  /*压缩之后的宽度*/
  var compressW = {
    quality: 0.7
  }
  /*一个是容器或者回调函数*/
  var objDiv
  /*创建FileReader对象*/
  var ready = new FileReader();
  /*异步读取文件内容，结果用data:url的字符串形式表示*/
  ready.readAsDataURL(file);
  /*文件读取完毕之后执行什么方法*/
  ready.onload = function () {
    /*返回值*/
    var result = this.result;
    /*运用canvas进行压缩*/
    /*创建image对象*/
    var img = new Image();
    img.src = result;
    img.onload = function() {
      var that = this;
      /*默认压缩比例*/
      var w = that.width / 4,
          h = that.height / 4
      /*生成canvas*/
      var canvas = document.createElement('canvas');
      var ctx = canvas.getContext('2d');
      canvas.width = w;
      canvas.height = h;
      /*画布上绘制图像、画布或视频*/
      ctx.drawImage(that,0,0,w,h);
      /*图像质量*/
      var quality = 0.6 //默认质量为0.7
      /*quality值越小,所绘制的图像质量越模糊*/
      var base64 = canvas.toDataURL('image/*',quality);
      /*回调函数返回base64的值*/
      objDiv = base64
      local = base64
      /*将以base64的图片url数据转换为Blod*/
      imgData = convertBase64UrlToBlob(objDiv)
    }
  }
}
/*返回数据 暂用延迟函数执行*/
export function returnImgData() {
  return imgData
}

/*用于预览*/
export function localBase64() {
  return local
}

function photoCompress(file,w,objDiv) {
  /*创建FileReader对象*/
  var ready = new FileReader();
  /*异步读取文件内容，结果用data:url的字符串形式表示*/
  ready.readAsDataURL(file);
  /*文件读取完毕之后执行什么方法*/
  ready.onload = function () {
    /*返回值*/
    var result = this.result;
    /*运用canvas进行压缩*/
    canvasDataURL(result,w,objDiv,file.name);
  }

}
function canvasDataURL(path,obj,callback,imgName) {
  /*创建image对象*/
  var img = new Image();
  img.src = path;
  img.onload = function() {
    var that = this;
    /*默认压缩比例*/
    var w = that.width,
      h = that.height,
      scale = w / h;
    w = obj.width || w;
    h = obj.width || (w / scale);
    var quality = 0.7 //默认质量为0.7
    /*生成canvas*/
    var canvas = document.createElement('canvas');
    var ctx = canvas.getContext('2d');
    /*创建属性节点*/
    var anw = document.createAttribute('width');
    var anh = document.createAttribute('height');
    /*属性节点值*/
    anh.nodeValue = h;
    canvas.setAttributeNode(anw);
    canvas.setAttributeNode(anh);
    /*画布上绘制图像、画布或视频*/
    ctx.drawImage(that,0,0,w,h);
    /*图像质量*/
    if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
      quality = obj.quality;
    }
    /*quality值越小,所绘制的图像质量越模糊*/
    var base64 = canvas.toDataURL('image/*',quality);
    /*回调函数返回base64的值*/
    callback = base64
    /*将以base64的图片url数据转换为Blod*/
    dataURLtoFile(callback,imgName)
  }
}
/*将以base64的图片url数据转换为Blod*/
function convertBase64UrlToBlob(urlData){
  var arr = urlData.split(','), mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
  while(n--){
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], {type:mime});
}

// 将base64转换成file对象
function dataURLtoFile (dataurl, filename) {
  filename = filename.split('.')
  let arr = dataurl.split(',')
  let mime = arr[0].match(/:(.*?);/)[1]
  let suffix = mime.split('/')[1]
  let bstr = atob(arr[1])
  let n = bstr.length
  let u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new File([u8arr], `${filename[0]}.${suffix}`, {type: mime})
}


