<template>
  <div class="recommendModel">
    <Model1 :realTimeNews="recommend.realTimeNews" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></Model1>
    <!--<Model2 :joke="recommend.recommendJoke"></Model2>-->
    <Model3 :imgs="recommend.recommendImgs" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></Model3>
    <Model4 :videos="recommend.recommendVideos" :PRIFIX="PRIFIX" :dynamicUrl="dynamicUrl"></Model4>
    <!--<FriendshipLink :about="recommend.about"></FriendshipLink>-->
    <FriendshipLink :PRIFIX="PRIFIX"></FriendshipLink>
  </div>
</template>

<script type="text/ecmascript-6">
  import Model1 from "./model1/model1.vue"
  import Model2 from "./model2/model2.vue"
  import Model3 from "./model3/model3.vue"
  import Model4 from "./model4/model4.vue"
  import FriendshipLink from "./link/link.vue"

  export default{
    props: {
      recommend: Object,
      PRIFIX: String,
      dynamicUrl: String
    },
    components: {
      Model1,
      Model2,
      Model3,
      Model4,
      FriendshipLink
    }
  }
</script>

<style lang="less" scoped>
  .recommendModel{
    flex: 1;
    margin-top: 8px;
    padding-bottom: 20px;
  }
</style>



