      },
      addGood(index,id) {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/dynamic/praise',qs.stringify({
          'dynamicId': id
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.userContent.dynamic.pageContent[index].good += 1
            self.userContent.dynamic.pageContent[index].goodFlag = 1
          } else if(response.code === 3) {
            self.$Notice.error({
              title: '请登录！',
            })
            let url = window.location.href + ''
            saveURL(url)
            setTimeout(function () {
              self.$router.push('/login')
            },500)
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})
      }
    },
    created() {
      this.userId = this.$route.query.idUser
      if(this.userId == this.userInfo.id) {
        this.$router.push('/personInfo')
      }
      this.$nextTick(() => {
        var qs = require('qs')
        var self = this
        this.$http.post(self.PRIFIX + '/api/personal/user/index',qs.stringify({
          'userId1': self.userId
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.otherUser = response.data
          } else {
            self.$Notice.error({
              title: '内容获取失败!!!',
            })
          }
        }).catch(function (error) {})
      })
    },
    mounted () {
      var i = 2
      var a = 2
      var b = 2
      var c = 2
      var d = 2
      this.$nextTick(() => {
        window.addEventListener('scroll', () => {
          /*内容高度*/
          let elHeight = this.$refs.el.clientHeight + 80
          /*滚动高度*/
          let height = 0
          if (document.body.scrollTop) {
            height = document.body.scrollTop
          }
          if (document.documentElement.scrollTop) {
            height = document.documentElement.scrollTop
          }
          if (height + window.innerHeight >= elHeight) {
            var qs = require('qs')
            var self = this
            if(this.tabName === "name1") {
              if(!this.contentFlag1) {
                this.$http.post(self.PRIFIX + '/api/personal/user/dynamic/list',qs.stringify({
                  'page': a,
                  'size': 6,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if(response.code == 0) {
                    if(response.data.dynamic.pageContent.length == 0){
                      self.contentFlag1 = true
                      self.loadCssFlag = false
                    } else {
                      let dynamic = response.data.dynamic.pageContent
                      dynamic.forEach((item) => {
                        self.userContent.dynamic.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  }
                  else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }
            } else if(this.tabName === "name5"){
              if(!this.contentFlag2) {
                this.$http.post(self.PRIFIX + '/api/personal/user/attention/list',qs.stringify({
                  'page': b,
                  'size': 6,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if(response.code == 0) {
                    if(response.data.attention.pageContent.length == 0){
                      self.contentFlag2 = true
                      self.loadCssFlag = false
                    } else {
                      let attention = response.data.attention.pageContent
                      attention.forEach((item) => {
                        self.userContent.attention.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  } else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }
            }else if (this.tabName === "name6") {
              if(!this.contentFlag3) {
                this.$http.post(self.PRIFIX + '/api/personal/user/fans/list',qs.stringify({
                  'page': b,
                  'size': 6,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if(response.code == 0) {
                    if(response.data.fans.pageContent.length == 0){
                      self.contentFlag3 = true
                      self.loadCssFlag = false
                    } else {
                      let fans = response.data.fans.pageContent
                      fans.forEach((item) => {
                        self.userContent.fans.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  } else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }

            }else if(this.tabName === "name2"){
              if(!this.contentFlag4) {
                this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
                  'page': i,
                  'size': 6,
                  'typeId': 1,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if (response.code === 0) {
                    if(response.data.pageContent.pageContent.length == 0){
                      self.contentFlag4 = true
                      self.loadCssFlag = false
                    } else {
                      response.data.pageContent.pageContent.forEach((item) => {
                        self.userContent.article.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  } else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }

            }else if(this.tabName === "name3"){
              if(!this.contentFlag5) {
                this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
                  'page': i,
                  'size': 6,
                  'typeId': 2,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if (response.code === 0) {
                    if(response.data.pageContent.pageContent.length == 0){
                      self.contentFlag5 = true
                      self.loadCssFlag = false
                    } else {
                      response.data.pageContent.pageContent.forEach((item) => {
                        self.userContent.imgs.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  } else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }
            }else if(this.tabName === "name4"){
              if(!this.contentFlag6) {
                this.$http.post(self.PRIFIX + '/api/personal/user/collected/article',qs.stringify({
                  'page': i,
                  'size': 6,
                  'typeId': 3,
                  'userId': self.userId
                })).then(function (response) {
                  response = response.data
                  if (response.code === 0) {
                    if(response.data.pageContent.pageContent.length == 0){
                      self.contentFlag6 = true
                      self.loadCssFlag = false
                    } else {
                      response.data.pageContent.pageContent.forEach((item) => {
                        self.userContent.video.pageContent.push(item)
                      })
                      self.loadCssFlag = false
                    }
                  } else {
                    self.$Notice.error({
                      title: '内容获取失败',
                    })
                    self.loadCssFlag = false
                  }
                }).catch(function (error) {})
              }
            }
            i++
            a++
            b++
            c++
            d++
          }
        })
      })
    }
  }

</script>

<style lang="less" scoped>

  .person-info {
    display: flex;
