    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .left {
    flex: 0 0 710px;
    width: 710px;
    margin-right: 20px;
  }

  .right {
    flex: 1;
    width: 340px;
  }

  .top_wrapper {
    position: relative;
    top: 80px;
    width: 1070px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .demo-auto-complete-item {
    padding: 4px 0;
