    border-radius: 10px;
    margin-top: -50px;
    z-index: 10;
  }

  .avatar-background {
    position: absolute;
    z-index: 11;
    font-size: 16px;
    text-align: center;
    line-height: 120px;
    width: 120px;
    height: 120px;
    background-color: rgba(0, 0, 0, 0.4);
    color: #ffffff;
    margin-top: -50px;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.4s;
    opacity: 0;
  }

  .person-content {
    display: inline-block;
    vertical-align: top;
    margin-left: 20px;
  }

  .person-content h2 {
    margin-top: 5px;
    margin-bottom: 10px;
  }

  .editor {
    display: inline-block;
    float: right;
    margin-top: 10px;
  }

  .margin-top-10px {
    margin-top: 10px;
  }

  .margin-bottom-10px {
    margin-bottom: 10px;
  }

  .name {
    margin-bottom: 5px;
    font-size: 13px;
    color: #222222;
  }

  .input-box {
    margin-bottom: 15px;
  }

  .notContent {
    text-align: center;
    color: #bbbec4;
    font-size: 18px;
  }

</style>



